import * as React from 'react';
import PropTypes from 'prop-types';
import {useTheme, styled} from '@mui/material/styles';
import Popper from '@mui/material/Popper';
import ClickAwayListener from '@mui/material/ClickAwayListener';
import DoneIcon from '@mui/icons-material/Done';
import Autocomplete, {autocompleteClasses, createFilterOptions} from '@mui/material/Autocomplete';
import ButtonBase from '@mui/material/ButtonBase';
import InputBase from '@mui/material/InputBase';
import Box from '@mui/material/Box';
import {useHistory} from "react-router-dom";
import {useCallback, useEffect, useState} from "react";
import {RiCoinsFill} from "@react-icons/all-files/ri/RiCoinsFill";
import axios from "axios";

const StyledAutocompletePopper = styled('div')(({theme}) => ({
    [`& .${autocompleteClasses.paper}`]: {
        boxShadow: 'none',
        margin: 0,
        color: 'inherit',
        fontSize: 13,
    },
    [`& .${autocompleteClasses.listbox}`]: {
        backgroundColor: theme.palette.mode === 'light' ? '#fff' : '#1c2128',
        padding: 0,
        [`& .${autocompleteClasses.option}`]: {
            minHeight: 'auto',
            alignItems: 'flex-start',
            padding: 8,
            borderBottom: `1px solid  ${
                theme.palette.mode === 'light' ? ' #eaecef' : '#30363d'
            }`,
            '&[aria-selected="true"]': {
                backgroundColor: 'transparent',
            },
            '&[data-focus="true"], &[data-focus="true"][aria-selected="true"]': {
                backgroundColor: theme.palette.action.hover,
            },
        },
    },
    [`&.${autocompleteClasses.popperDisablePortal}`]: {
        position: 'relative',
    },
}));

function PopperComponent(props) {
    const {disablePortal, anchorEl, open, ...other} = props;
    return <StyledAutocompletePopper {...other} />;
}

PopperComponent.propTypes = {
    anchorEl: PropTypes.any,
    disablePortal: PropTypes.bool,
    open: PropTypes.bool.isRequired,
};

const StyledPopper = styled(Popper)(({theme}) => ({
    border: `1px solid ${theme.palette.mode === 'light' ? '#e1e4e8' : '#30363d'}`,
    boxShadow: `0 8px 24px ${
        theme.palette.mode === 'light' ? 'rgba(149, 157, 165, 0.2)' : 'rgb(1, 4, 9)'
    }`,
    borderRadius: 6,
    width: 300,
    zIndex: theme.zIndex.modal,
    fontSize: 13,
    color: theme.palette.mode === 'light' ? '#24292e' : '#c9d1d9',
    backgroundColor: theme.palette.mode === 'light' ? '#fff' : '#1c2128',
}));

const StyledInput = styled(InputBase)(({theme}) => ({
    padding: 10,
    width: '100%',
    borderBottom: `1px solid ${
        theme.palette.mode === 'light' ? '#eaecef' : '#30363d'
    }`,
    '& input': {
        borderRadius: 4,
        backgroundColor: theme.palette.mode === 'light' ? '#fff' : '#0d1117',
        padding: 8,
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        border: `1px solid ${theme.palette.mode === 'light' ? '#eaecef' : '#30363d'}`,
        fontSize: 14,
        '&:focus': {
            boxShadow: `0px 0px 0px 3px ${
                theme.palette.mode === 'light'
                    ? 'rgba(6,27,69,0.3)'
                    : 'rgb(12, 45, 107)'
            }`,
            borderColor: theme.palette.mode === 'light' ? '#30363d' : '#388bfd',
        },
    },
}));

const Button = styled(ButtonBase)(({theme}) => ({
    fontSize: 13,
    width: '100%',
    textAlign: 'left',
    paddingBottom: 8,
    color: theme.palette.mode === 'light' ? '#90a1b4' : '#8b949e',
    fontWeight: 600,
    '&:hover,&:focus': {
        color: theme.palette.mode === 'light' ? '#adc1d7' : '#58a6ff',
    },
    '& span': {
        width: '100%',
    },
    '& svg': {
        width: 16,
        height: 16,
    },
}));

export default function GitHubLabel(props) {

    const history = useHistory();
    const [marketList, setMarketList] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);


    const fetchCoinHandler = useCallback(async () => {
        setIsLoading(true);
        setError(null);
        try {

            const listResponse = await axios.get('currencies/list');

            if (listResponse.status !== 200) {
                throw new Error('Something went wrong with CoinList!');
            }


            const marketL = (listResponse.data);


            const loadedList = [];


            for (var keys in marketL) {
                if (keys != 'insert' && marketL[keys].markets != undefined && marketL[keys].markets.length > 0) {
                    loadedList.push({
                            name: marketL[keys].short_name,
                            market: marketL[keys].markets
                        }
                    )
                }
            }

            setMarketList(loadedList);

        } catch (error) {
            setError(error.message);
        }
        setIsLoading(false);
    }, []);

    useEffect(() => {
        fetchCoinHandler();
    }, [fetchCoinHandler]);


    const filterOptions = createFilterOptions({
        matchFrom: 'start'
    });

    //***************************************************************************************************************

    const [anchorEl, setAnchorEl] = React.useState(null);
    const [value, setValue] = React.useState([]);
    const [pendingValue, setPendingValue] = React.useState();
    const theme = localStorage.getItem('themeMode');

    const handleClick = (event) => {
        setPendingValue(value);
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setValue(pendingValue);
        if (anchorEl) {
            anchorEl.focus();
        }
        setAnchorEl(null);
    };

    const choseCoin = (Value) => {

        history.replace("/Trade?pair=" + Value);
        history.go();

    }


    const open = Boolean(anchorEl);
    const id = open ? 'github-label' : undefined;

    return (
        <React.Fragment>
            <Box sx={{width: 150, fontSize: 36}}>
                <Button sx={{fontSize: 18}} disableRipple aria-describedby={id} onClick={handleClick}>
                    {props.Name}
                </Button>
            </Box>
            <StyledPopper id={id} open={open} anchorEl={anchorEl} placement="bottom-start">
                <ClickAwayListener onClickAway={handleClose}>
                    <div className={`${theme == 'light' ? 'searchBarMarket-light' : 'searchBarMarket-dark'}`}>
                        {/*<Box*/}
                        {/*    sx={{*/}
                        {/*        borderBottom: `1px solid ${*/}
                        {/*            theme.palette.mode === 'light' ? '#eaecef' : '#30363d'*/}
                        {/*        }`,*/}
                        {/*        padding: '8px 10px',*/}
                        {/*        fontWeight: 600,*/}
                        {/*    }}*/}
                        {/*>*/}
                        {/*    Apply labels to this pull request*/}
                        {/*</Box>*/}
                        <Autocomplete
                            open
                            multiple
                            value={pendingValue}
                            onChange={(event, newValue, reason) => {
                                choseCoin(newValue[0].market[0]);
                                setPendingValue(newValue);
                            }}
                            disableCloseOnSelect
                            PopperComponent={PopperComponent}
                            renderTags={() => null}
                            noOptionsText="No labels"
                            renderOption={(props, option, {selected}) => (
                                <li {...props}>
                                    <Box
                                        sx={{width: 17, height: 17, mr: '5px', ml: '-2px'}}
                                        style={{
                                            visibility: selected ? 'visible' : 'hidden',
                                        }}
                                    />
                                    <Box
                                        component="span"
                                        sx={{
                                            width: 16,
                                            height: 16,
                                            flexShrink: 0,
                                            borderRadius: '3px',
                                            mr: 1,
                                            mb: '2px',
                                        }}
                                    >
                                        {/*<img className="img-fluid" src="img/BTC_Ico.png" />*/}
                                        <RiCoinsFill/>
                                    </Box>
                                    <Box
                                        sx={{
                                            fontSize: 16,
                                            flexGrow: 1,
                                            fontWeight:550,
                                            '& span': {
                                                color:
                                                    theme == 'light' ? '#181818' : '#4a4949',
                                            },
                                        }}
                                    >
                                        {option.name} <span  className={`${theme == 'light' ? 'MarketSearch-coin-light' : 'MarketSearch-coin-dark'}`}>/ {option.market[0].split(option.name)}</span>
                                        <br/>
                                    </Box>
                                    <Box
                                        sx={{opacity: 0.6, width: 18, height: 18}}
                                        style={{
                                            visibility: selected ? 'visible' : 'hidden',
                                        }}
                                    />
                                </li>
                            )}
                            options={marketList}
                            getOptionLabel={(option) => option.market[0]}
                            renderInput={(params) => (
                                <StyledInput
                                    ref={params.InputProps.ref}
                                    inputProps={params.inputProps}
                                    autoFocus
                                    placeholder="Filter labels"
                                />
                            )}
                        />
                    </div>
                </ClickAwayListener>
            </StyledPopper>
        </React.Fragment>
    );
}

