import React, {useContext} from "react";
import NavBar from "../widgets/NavBar";
import '../css/bootstrap.css';
import '../css/FAQ.css';
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper";


export default function FAQScreen(props) {

  const context =useContext(Context);

  return (
    <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
     <NavBar />
      <br/>
      <br/>
      <br/>
      <br/>
      <div className="container-fluid ">
      <div className="row justify-content-center">
        <div className="col-md-12 col-lg-12 col-xl-10  col-sm-12 col-xs-12">
          <div className="card " dir={context.locale == "fa" && "rtl"}>
            <div className="row justify-content-between">
              <div className="card-body col-lg-6 explanation-container">
                <h2 className="card-title mb-5">
                  <FormattedMessage
                      id="FAQScreen.Title"
                      defaultMessage="FAQ About Withdrawal"
                  />
                </h2>
                <div className="card-text">
                  <h5>
                    <FormattedMessage
                        id="FAQScreen.SubTitle"
                        defaultMessage="How long does it take my withdrawal to arrive?"
                    />
                  </h5>
                  <FormattedMessage
                      id="FAQScreen.ThreeProcedures"
                      defaultMessage="Three Procedures"
                  />
                  <br />{" "}
                  <strong>
                    <FormattedMessage
                        id="FAQScreen.BlockConfirmations"
                        defaultMessage="BlockConfirmations"
                    />
                  </strong>
                  <br />
                  <FormattedMessage
                      id="FAQScreen.BlockConfirmations.Explanation"
                      defaultMessage="FAQScreen.BlockConfirmations.Explanation"
                  />
                </div>
                <h2 className="card-title mb-5 mt-5">
                  <FormattedMessage
                      id="FAQScreen.BlockConfirmations.FAQAboutDeposit"
                      defaultMessage="FAQScreen.BlockConfirmations.FAQAboutDeposit"
                  />
                </h2>
                <div className="card-text">
                  <p></p>
                  <h5>
                    <FormattedMessage
                        id="FAQScreen.BlockConfirmations.Explanation.sec"
                        defaultMessage="FAQScreen.BlockConfirmations.Explanation.sec"
                    />
                  </h5>
                  <br />{" "}

                  <FormattedMessage
                      id="FAQScreen.UsersCan"
                      defaultMessage="Users Can"
                  />
                  <p />
                </div>
              </div>
              <div className="card-body col-lg-6 coins-table-container">
                <div className=" mt-3" style={{ overflowX: "auto" }}>
                  <table>
                    <tbody>
                    <tr className="FAQ-table-titles">
                      <th>
                        <FormattedMessage
                            id="FAQScreen.Table.CoinType"
                            defaultMessage="CoinType"
                        />
                      </th>
                      <th>
                        <FormattedMessage
                            id="FAQScreen.Table.MainnetType"
                            defaultMessage="MainnetType"
                        />
                      </th>
                      <th>
                        <FormattedMessage
                            id="FAQScreen.Table.MinimumWithdrawal"
                            defaultMessage="MinimumWithdrawal"
                        />
                      </th>
                      <th>
                        <FormattedMessage
                            id="FAQScreen.Table.WithdrawalFee"
                            defaultMessage="WithdrawalFee"
                        />
                      </th>
                      <th>
                        <FormattedMessage
                            id="FAQScreen.Table.Explorer"
                            defaultMessage="Explorer"
                        />
                      </th>
                    </tr>
                    <tr className="FAQ-table-body">
                      <td>Jill</td>
                      <td>Smith</td>
                      <td>50</td>
                      <td>50</td>
                      <td>50</td>
                    </tr>
                    <tr className="FAQ-table-body">
                      <td>Eve</td>
                      <td>Jackson</td>
                      <td>94</td>
                      <td>94</td>
                      <td>94</td>
                    </tr>
                    <tr className="FAQ-table-body">
                      <td>Adam</td>
                      <td>Johnson</td>
                      <td>67</td>
                      <td>67</td>
                      <td>67</td>
                    </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


    </div>
  );
}
