import React, {useCallback, useContext, useEffect, useRef, useState} from 'react';
import NavBar from "../widgets/NavBar";
import "../css/Convert.css";
import "../css/bootstrap.css";
import {FormattedMessage} from "react-intl";
import {MdCompareArrows} from "@react-icons/all-files/md/MdCompareArrows";
import {Context} from "../../translation/Wrapper";
import MarketSearchSec from "../widgets/MarketSearchSec";
import {LoginContext} from "../../Root";
import {useHistory} from "react-router-dom";
import {parseNumber} from "../../Extentions";
import axios from "axios";
import {CenterModal, ModalTitle} from "react-spring-modal";
import {useToasts} from "react-toast-notifications";
import LoadingDialog from "../widgets/LoadingDialog";
import {CircularProgress} from "@material-ui/core";

const Convert = () => {

    const history = useHistory();
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
    const [isLoading, setIsLoading] = useState(false);
    const [currentPriceUSD, setCurrentPriceUSD] = useState();
    const [swap, setSwap] = useState(true);

    const [first, setFirst] = useState();
    const [second, setSecond] = useState();
    const [price, setPrice] = useState();
    const priceRef = useRef("");

    const [isBuyMode, setIsBuyMode] = useState(true);
    const [availableCoin, setAvailableCoin] = useState("--");
    const [availableCoinName, setAvailableCoinName] = useState(" ");
    const [convertAmount, setConvertAmount] = useState();
    const [loading, setLoading] = useState(false);
    const [calculated, setCalculated] = useState();

    const getMarketDetail = useCallback(async (val) => {

        if (val != undefined) {
            setAvailableCoinName(val);
            setLoading(true);
            await axios.get(`core/balanceInfo`, {
                headers: {
                    'Authorization': "Bearer " + localStorage.getItem("token")
                }
            }).then((res) => {
                let loadedData;
                if (res.data.data[val]) {
                    loadedData = res.data.data[val].available;
                } else {
                    loadedData = 0;
                }
                setAvailableCoin(loadedData);
            });
            setLoading(false);
        }
    }, []);

    useEffect(() => {
        if (!isLoggedIn) {
            history.push('login');
        }

    }, []);

    const {addToast} = useToasts();

    const context = useContext(Context);

    const notMatch = <p className="text-danger ms-4 text-capitalize fw-bold">the selected coins must be different</p>;


    const setupWebSockets = (marketData) => {
        setLoading(true);
        setPrice("");
        const ws = new WebSocket("wss://socket.sigloyex.com");
        ws.onmessage = function (event) {
            const data = JSON.parse(event.data);
            if (data.method == "state.update") {
                //setCurrentPrice(data.params[0][marketData].last);
                if (marketData != "USDTUSDT" && marketData != "usdtUSDT") {
                    setCurrentPriceUSD(data.params[0][marketData].last);
                } else {
                    setCurrentPriceUSD(1);
                }

            }
        };

        ws.onopen = function (event) {
            // //console.log(event);
            // const msg = JSON.stringify({
            //   method: "server.ping",
            //   params: [],
            //   id: 11,
            // });
            // ws.send(msg);
            ws.send(
                JSON.stringify({
                        "method": "state.subscribe",
                        "params": [
                            marketData
                        ],
                        "id": 15
                    }
                )
            );

        };
        setLoading(false);
    };

    const Calculator = (Price) => {
        if (Price && currentPriceUSD) {
            setCalculated(Price * currentPriceUSD + "  USDT");
        }
    }


    const convert = useCallback(async (fi, sec) => {
        setIsLoading(true);
        const Amount = priceRef.current.value;
        const tokken = "bearer " + localStorage.getItem("token");
        const ConvertOrder = {
            "fromcurrency": fi,
            "tocurrency": sec,
            "amount": Amount
        }


        const response = await fetch(`http://31.192.237.52:8080/convert/check_convert_amount`, {
            method: 'POST',
            body: JSON.stringify(ConvertOrder),
            headers: {
                'Content-Type': 'application/json',
                'Authorization': tokken,
            }
        });
        const data = await response.json();
        if (data.status != "ok") {
            addToast(data.errors[0].msg, {appearance: "error", autoDismiss: true});
        } else {
            setConvertAmount(data.data.convert_value);
            setOpen(true);
        }
        setIsLoading(false);
    }, [])

    async function addConvert() {
        setIsLoading(true);
        console.log("hi")
        const Amount = priceRef.current.value;
        const tokken = "bearer " + localStorage.getItem("token");
        const ConvertOrder = {
            "fromcurrency": first,
            "tocurrency": second,
            "amount": Amount
        }
        console.log(ConvertOrder);
        const response = await fetch(`http://31.192.237.52:8080/convert/convert`, {
        method: 'POST',
            body: JSON.stringify(ConvertOrder),
            headers: {
            'Content-Type': 'application/json',
                'Authorization': tokken,
        }
    });
    const data = await response.json();

    if (data.status != "ok") {
        addToast(data.errors[0].msg, {appearance: "error", autoDismiss: true});
        setOpen(false);
    } else {
        console.log(data);
        addToast('Your Order Was successful', {appearance: "success", autoDismiss: true});
        setPrice("");
        setOpen(false);
        getMarketDetail();
    }
        setIsLoading(false);
    }

    let content ;
    if (isLoading){
        content = <CircularProgress size={16}/>;
    }

    const [isOpen, setOpen] = useState(false);

    return (
        <div>
            <NavBar/>

            <div className=" header col-xs-12 col-md-12 col-sm-12 mt-5" id="header">
                <div className="container-fluid">
                    <div className="header-title ">
                        <h2 className="card-title">Convert</h2>
                    </div>
                    <br/>
                    <br/>
                </div>
            </div>

            <div className="container-fluid ">
                <div className="row justify-content-center">
                    <div className="col-md-10 col-lg-10 col-xl-10 col-xxl-8 col-sm-12 col-xs-12">
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <div className="card CovertCard border-0">

                            <div className="card-body mb-5 shadow">

                                <br/>
                                <br/>
                                <br/>


                                {/*<button className=" col-2 d-flex justify-content-center convertIcon"*/}
                                {/*        onClick={() => {*/}
                                {/*            setSwap(!swap);*/}
                                {/*            setIsBuyMode(!isBuyMode);*/}
                                {/*            setAvailableCoinName("");*/}
                                {/*            setAvailableCoin(0);*/}
                                {/*        }}>*/}
                                {/*    <MdCompareArrows/>*/}
                                {/*</button>*/}


                                <form onSubmit={addConvert}>
                                    <div className="row justify-content-around">
                                        <div className="col-4 marketBarContainer mt-3">
                                            <label className="d-flex justify-content-between ">From </label>
                                            <MarketSearchSec onSocket={setupWebSockets}
                                                             onGetMarketDetail={getMarketDetail}
                                                             setFirst={setFirst}
                                                             lable={"From"}
                                            />
                                        </div>
                                        <div className="col-4 marketBarContainer mt-3">
                                            <label>To</label>
                                            <MarketSearchSec market={true} setSecond={setSecond} lable={"To"}/>
                                        </div>
                                    </div>
                                    <div className="row justify-content-around mt-3">
                                        <div className="col-4 toConvert">
                                            <input type="text" className="form-control"
                                                   placeholder="Please enter 0.0003-50" disabled={first == second}
                                                   value={price}
                                                   ref={priceRef}
                                                   onChange={(e) => {
                                                       setPrice(e.target.value);
                                                       Calculator(e.target.value);
                                                   }}
                                            />
                                            <p
                                                className={` m-0 text-secondary d-inline`}>
                                                {first == second && first != undefined && notMatch}
                                                {availableCoin + " " + availableCoinName}
                                            </p>
                                        </div>
                                        <div className="col-4 toConvert ">
                                            <input type="text"
                                                   className={`form-control ${!calculated ? 'disabledInput' : 'disabledInputCompleted fw-bold'}`}
                                                   placeholder="Please enter 0.0003-50"
                                                   value={calculated}/>
                                        </div>

                                    </div>

                                    <CenterModal isOpen={isOpen} >
                                        <button className="btn-close" onClick={() => setOpen(false)}></button>
                                        <div className={`modalContent ${isLoading && 'disabledButton'}`}>
                                        <ModalTitle style={{textAlign: "center"}}>Check Amount</ModalTitle>
                                        <p className="convertText">Your Calculated Amount is : <br/><span className="convertAmount">{convertAmount}</span></p>
                                        <button type="submit" onClick={addConvert} className=" convertBtn p-3">Convert {content} </button>
                                        </div>
                                    </CenterModal>

                                </form>

                                <br/>
                                <a className="d-flex justify-content-center">
                                    <button
                                        className={`bnConvert ${calculated && 'bn25'}  col-md-4 col-xs-8 col-sm-6 col-xl-3 ${!calculated && 'disabledInput'} `}
                                        onClick={() => convert(first, second)}>
                                        Place
                                    </button>
                                </a>
                                <br/>
                                <br/>
                                <br/>
                            </div>
                        </div>

                        <br/>
                        <br/>
                        <br/>

                    </div>
                </div>
            </div>

        </div>
    );
};

export default Convert;
