import React, {useState, useEffect, useContext, useCallback} from "react";
import '../css/bootstrap.css';
import '../css/walletOverview.scss';
import NavBar from "../widgets/NavBar";
import Footer from "../widgets/Footer";
import {buildStyles, CircularProgressbar} from "react-circular-progressbar";
import {Doughnut, Pie} from 'react-chartjs-2';
import {FaEye} from '@react-icons/all-files/fa/FaEye';
import {FaEyeSlash} from '@react-icons/all-files/fa/FaEyeSlash';
import {TiArrowUnsorted} from '@react-icons/all-files/ti/TiArrowUnsorted';
import {FaAngleDoubleRight} from '@react-icons/all-files/fa/FaAngleDoubleRight';
import {Button, CircularProgress, ListItem, ListItemText, Menu, MenuItem} from "@material-ui/core";
import {FormattedMessage, FormattedDate} from 'react-intl';
import {Context} from "../../translation/Wrapper";
import {useHistory} from "react-router-dom";
import {LoginContext} from "../../Root";
import axios from "axios";

export default function WalletOverview(props) {

    const percentage = 55;
    const [eye, setEye] = useState(true);
    const [showTabs, setShowTabs] = useState(true);

    const [assets, setAssets] = useState([]);
    const [total, setTotal] = useState(0);
    const [networks, setNetworks] = useState([]);

    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);


    const [hideAssets, setHideAssets] = useState(false);

    const removeSmallAssets = () => {
        setHideAssets(!hideAssets);
    }

    const context = useContext(Context);

    const history = useHistory();
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);


    const data = {
        labels: [" % Spot Asset", " % Margin Asset", " % Perpetual Asset"],
        datasets: [{
            backgroundColor: [
                "#b91d47",
                "#2b5797",
                "#1e7145"
            ],
            hoverBackgroundColor: [
                "#de2655",
                "#3d76cd",
                "#2eaa69"
            ],
            data: [55, 49, 44]
        }]
    };

    const fetchCoinHandler = useCallback(async () => {
        setIsLoading(true);
        setError(null);

        try {
            const listResponse = await axios.get('currencies/list');

            if (listResponse.status !== 200) {
                throw new Error('Something went wrong with CoinList!');
            }


            const marketL = (listResponse.data);

            console.log(listResponse);

            const Response = await axios.get('core/balanceInfo' ,{
                headers:{
                    'Authorization': "Bearer " + localStorage.getItem("token")
                }
            });

            console.log(Response);

            if (Response.status !== 200) {
                throw new Error('Something went wrong with CoinList!');
            }

            const balanceInfo = (Response.data.data);

            const response = await axios.get('core/marketTickerAll');

            if (response.status !== 200) {
                throw new Error('Something went wrong!');
            }

            const tickerAll = (response.data.data.ticker);

            const loadedList = [];


            for (var keys in marketL) {
                for (var i in balanceInfo) {
                    for (var j in tickerAll) {
                        if (marketL[keys].short_name != null && marketL[keys].short_name == i && (marketL[keys].short_name) + "USDT" == j) {
                             loadedList.push({
                                name: marketL[keys].short_name,
                                available: balanceInfo[i].available,
                                frozen: balanceInfo[i].frozen,
                                value: tickerAll[j].last,
                                smallAsset: true
                            });
                        }
                    }
                }
            }

            for (var keys in marketL) {
                for (var i in balanceInfo) {
                    if (marketL[keys].short_name != null && marketL[keys].short_name == "USDT" && i == "USDT") {
                         loadedList.push({
                            name: marketL[keys].short_name,
                            available: balanceInfo[i].available,
                            frozen: balanceInfo[i].frozen,
                            value: 1,
                            smallAsset: true
                        });
                    }
                }
            }


            const operation = (list1, list2, isUnion = false) =>
                list1.filter(
                    (set => a => isUnion !== set.has(a.short_name))(new Set(list2.map(b => b.name)))
                );

// Following functions are to be used:
            const inBoth = (list1, list2) => operation(list1, list2, true)

            const difference = inBoth(marketL, loadedList);

            for (var j in difference) {
                if (difference[j].short_name != undefined) {
                     loadedList.push({
                        name: difference[j].short_name,
                        available: 0,
                        frozen: 0,
                        value: 0,
                        smallAsset: false
                    });
                }
            }

            setAssets(loadedList);
            let sum = loadedList.reduce((accumulator, current) => accumulator + parseFloat(current.value) * (parseFloat(current.available) + parseFloat(current.frozen)), 0.00);

            setTotal(Math.floor(sum * 100) / 100);

        } catch (error) {
            setError(error.message);
        }
        setIsLoading(false);
    }, []);

    let content;

    if (assets.length === 0) {
        content = <p>Found no Coin.</p>;
    }

    if (error) {
        content = <p>{error}</p>;
    }

    if (isLoading) {
        content = <p><CircularProgress /></p>;
    }

    useEffect(() => {
        if (!isLoggedIn){
            history.push('login');
        }
        else {
            fetchCoinHandler();
        }
    }, []);


    return (
        <div>
            <NavBar/>
            <br/>
            <br/>
            <br/>
            <br/>

            <div className="container-fluid ">
                <div className="row justify-content-center">
                    <div className="col-md-10 col-lg-10 col-xl-8 col-xxl-8 col-sm-12 col-xs-12">
                        <div className="card shadow total-assets">
                            <div className="card-header d-flex justify-content-between"
                                 dir={context.locale == "fa" && "rtl"}>
                                <FormattedMessage
                                    id="Wallet.Title"
                                    defaultMessage="Wallet status"
                                />


                            </div>
                            <div className="card-body ">
                                <div className="row ">
                                    <div className="card-header status pe-0">
                                        <div className="justify-content-between justify-content-sm-start d-flex">
                                            <div className="Est-total me-5 ms-1">
                                                <p className="title m-0">
                                                    <FormattedMessage
                                                        id="Wallet.TotalAssets"
                                                        defaultMessage="Est. Total Assets "
                                                    />{" "}
                                                    <span onClick={() => {
                                                        setEye(!eye)
                                                    }} className="eye-ico ps-4">
                                                        {eye ? <FaEye/> : <FaEyeSlash/>}
                    </span>
                                                </p>
                                                {eye ? total : '**********'}
                                                <span className="crypto-type">USDT </span>
                                            </div>
                                            <div className="Est-percent me-5 ms-1">
                                                <p className="title m-0">
                                                    <FormattedMessage
                                                        id="Wallet.PNL"
                                                        defaultMessage="Last 24H PNL"
                                                    />
                                                </p>{eye ? '- -' : '* *'}
                                                <span className="crypto-type">%</span>
                                            </div>
                                        </div>
                                        <div className="d-grid gap-2 d-md-block d-flex mt-5 ">
                                            <button className="withdraw" type="button"
                                                    onClick={() => history.push("withdraw")}>
                                                <FormattedMessage
                                                    id="Wallet.Withdraw"
                                                    defaultMessage="SpotWallets"
                                                />
                                            </button>
                                            <button className="Deposit" type="button"
                                                    onClick={() => history.push("deposit")}>
                                                <FormattedMessage
                                                    id="Wallet.Deposit"
                                                    defaultMessage="MarginWallets"
                                                />
                                            </button>
                                            <button className="Transfer" type="button" disabled={true}>
                                                <FormattedMessage
                                                    id="Wallet.Transfer"
                                                    defaultMessage="WalletPerpetualWallets"
                                                />
                                            </button>
                                        </div>
                                    </div>
                                    <div className="chart-container ">

                                        <Doughnut
                                            height={200}
                                            width={200}

                                            data={data}

                                            options={{
                                                responsive: false,
                                                layouts: {
                                                    padding: {
                                                        top: 50
                                                    }
                                                },
                                                plugins: {
                                                    legend: {
                                                        display: false,
                                                        position: 'bottom'
                                                    },
                                                    title: {
                                                        display: true,
                                                        position: 'bottom',
                                                        text: ["assets", '( USDT )']

                                                    },
                                                    animation: {
                                                        duration: 0
                                                    }
                                                }
                                            }}
                                        />


                                    </div>
                                    <div className="pie-progress-container-wallet">
                                        <div className={`pie_progress `}>
                                            <CircularProgressbar styles={buildStyles({


                                                // Whether to use rounded or flat corners on the ends - can use 'butt' or 'round'
                                                strokeLinecap: 'butt',

                                                // Text size
                                                textSize: '18px',


                                                // How long animation takes to go from one percentage to another, in seconds
                                                pathTransitionDuration: 0.5,

                                                // Can specify path transition in more detail, or remove it entirely
                                                // pathTransition: 'none',

                                                // Colors
                                                pathColor: `#4d88ff`,
                                                textColor: '#717171',
                                                trailColor: '#d6d6d6',
                                                backgroundColor: '#4d88ff',
                                            })}
                                                                 value={percentage} text={`${percentage}%`}/>
                                        </div>

                                        <div className="pie_progress-title text-capitalize"
                                             dir={context.locale == "fa" && "rtl"}>
                                            <FormattedMessage
                                                id="Wallet.ProgressBar.24HWithdrawLimit"
                                                defaultMessage="24H Withdraw Limit"
                                            />
                                        </div>
                                        <p className="withdraw-amount text-capitalize">250/500</p>
                                    </div>
                                    {/*                        <div>*/}
                                    {/*                            <button id="button_start">start()</button>*/}
                                    {/*                            <button id="button_stop">stop()</button>*/}
                                    {/*                            <button id="button_go">go('50')</button>*/}
                                    {/*                            <button id="button_go_percentage">go('50%')</button>*/}
                                    {/*                            <button id="button_finish">finish()</button>*/}
                                    {/*                            <button id="button_reset">reset()</button>*/}
                                    {/*                        </div>*/}
                                </div>
                            </div>
                        </div>
                        <div className="card shadow financial-asset mt-2">
                            <div className="d-grid gap-2 d-md-block d-flex  mt-2 "
                                 dir={context.locale == "fa" && "rtl"}>
                                <button onClick={() => {
                                    setShowTabs(!showTabs)
                                }}
                                        type="button" className="btn Accounts-container">
                                    <FormattedMessage
                                        id="Wallets.Btn.container"
                                        defaultMessage="Wallets"
                                    />
                                </button>
                                <div
                                    className={`form-check ${context.locale != "fa" && "float-end"} me-md-5 ms-2 mt-2`}>
                                    <input
                                        className="form-check-input"
                                        type="checkbox"
                                        defaultValue
                                        id="flexCheckDefault"
                                        onChange={removeSmallAssets}
                                    />
                                    <label className={`form-check-label ${context.locale == "fa" && "float-start"}`}
                                           htmlFor="flexCheckDefault">
                                        <FormattedMessage
                                            id="Wallet.HideSmallAssets"
                                            defaultMessage="HideSmallAssets"
                                        />
                                    </label>
                                </div>
                            </div>
                            <div className="card-header ">
                                {showTabs &&
                                <ul className="nav nav-tabs card-header-tabs" dir={context.locale == "fa" && "rtl"}>
                                    <li className="nav-item Accounts">
                                        <div className="nav-link active" aria-current="true">
                                            <span className="coloring"/>
                                            <FormattedMessage
                                                id="Wallet.SpotWallets"
                                                defaultMessage="SpotWallets"
                                            />{" "}
                                            <i className="fas fa-sort-down"/>
                                        </div>
                                    </li>
                                    <li className="nav-item Accounts disabled" aria-disabled>
                                        <div className="nav-link " aria-current="true">
                                            <FormattedMessage
                                                id="Wallet.MarginWallets"
                                                defaultMessage="MarginWallets"
                                            />
                                            <i className="fas fa-sort-down"/>
                                        </div>
                                    </li>
                                    <li className="nav-item Accounts disabled" aria-disabled>
                                        <div className="nav-link " aria-current="true">
                                            <FormattedMessage
                                                id="Wallet.PerpetualWallets"
                                                defaultMessage="PerpetualWallets"
                                            />
                                            <i className="fas fa-sort-down"/>
                                        </div>
                                    </li>
                                </ul>
                                }
                            </div>
                            <div className="card-body">
                                <div
                                    className="tab-pane  crypto-table"
                                    id="market-table"
                                    role="tabpanel"
                                    aria-labelledby="pills-home-tab"
                                >
                                    <table className=" bg-transparent text-center ">
                                        <thead>
                                        <tr className="wallet-crypto-property ">
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="Wallet.WalletTable.Name"
                                                    defaultMessage="Name"
                                                />{" "}
                                                <span>
                      <TiArrowUnsorted className="fas fa-sort"/>
                    </span>
                                            </th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="Wallet.WalletTable.Amount"
                                                    defaultMessage="Amount"
                                                />{" "}
                                                <span>
                      <TiArrowUnsorted className="fas fa-sort"/>
                    </span>
                                            </th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="Wallet.WalletTable.Available"
                                                    defaultMessage="Available"
                                                />{" "}
                                                <span>
                      <TiArrowUnsorted className="fas fa-sort"/>
                    </span>
                                            </th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="Wallet.WalletTable.Frozen"
                                                    defaultMessage="Frozen"
                                                />
                                                <span>
                      <TiArrowUnsorted className="fas fa-sort"/>
                    </span>
                                            </th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="Wallet.WalletTable.MarketValue"
                                                    defaultMessage="MarketValue"
                                                />
                                                <span>
                      <TiArrowUnsorted className="fas fa-sort"/>
                    </span>
                                            </th>
                                            <th scope="col"/>
                                        </tr>
                                        </thead>

                                        <tbody >
                                        {assets.map((asset) => (
                                            (hideAssets === false ? (
                                                        <tr className="first-row wallet-glow">
                                                            <td className="d-flex justify-content-center">
                                                                <p>{asset.name}</p>
                                                            </td>
                                                            <td className="Amount">{eye ? parseFloat(asset.available) + parseFloat(asset.frozen) : '****'}</td>
                                                            <td className="Available ">{eye ? asset.available : '****'}</td>
                                                            <td className="Frozen">{eye ? asset.frozen : '****'}</td>
                                                            <td className="MarketValue">{eye ? Math.floor(parseFloat(asset.value) * (parseFloat(asset.available) + parseFloat(asset.frozen)) * 100) / 100 + ' USDT' : '****'}</td>
                                                            <td className="menu">
                                                                <div className="dropdown">
                                                                    <button className="menu-title btn "
                                                                            aria-controls="simple-menu"
                                                                            aria-haspopup="true">
                                                                        <FaAngleDoubleRight
                                                                            className="fas ACCIco fa-angle-double-right"/>
                                                                    </button>
                                                                </div>
                                                                <button
                                                                    type="button"
                                                                    className="btn btn-outline-secondary table-Trade"
                                                                >
                                                                    <FormattedMessage
                                                                        id="Wallet.WalletTable.trade"
                                                                        defaultMessage="Trade"
                                                                    />
                                                                </button>
                                                                <ul className="menu-dropdown">
                                                                    <ListItem
                                                                        onClick={() => history.push("deposit")}><ListItemText>Deposit</ListItemText></ListItem>
                                                                    <ListItem
                                                                        onClick={() => history.push("withdraw")}><ListItemText>Withdraw</ListItemText></ListItem>
                                                                    <ListItem
                                                                        onClick={() => history.push("Trade?pair=btcusdt")}><ListItemText>Exchange</ListItemText></ListItem>
                                                                </ul>
                                                            </td>
                                                        </tr>
                                                    )
                                                    :
                                                    asset.smallAsset && (
                                                        <tr className="first-row wallet-glow">
                                                            <td className="d-flex justify-content-center">
                                                                <p>{asset.name}</p>
                                                            </td>
                                                            <td className="Amount">{eye ? parseFloat(asset.available) + parseFloat(asset.frozen) : '****'}</td>
                                                            <td className="Available ">{eye ? asset.available : '****'}</td>
                                                            <td className="Frozen">{eye ? asset.frozen : '****'}</td>
                                                            <td className="MarketValue">{eye ? Math.floor(parseFloat(asset.value) * (parseFloat(asset.available) + parseFloat(asset.frozen)) * 100) / 100 + ' USDT' : '****'}</td>
                                                            <td className="menu">
                                                                <div className="dropdown">
                                                                    <button className="menu-title btn "
                                                                            aria-controls="simple-menu"
                                                                            aria-haspopup="true">
                                                                        <FaAngleDoubleRight
                                                                            className="fas ACCIco fa-angle-double-right"/>
                                                                    </button>
                                                                </div>
                                                                <button
                                                                    type="button"
                                                                    className="btn btn-outline-secondary table-Trade"
                                                                >
                                                                    <FormattedMessage
                                                                        id="Wallet.WalletTable.trade"
                                                                        defaultMessage="Trade"
                                                                    />
                                                                </button>
                                                                <ul className="menu-dropdown">
                                                                    <ListItem
                                                                        onClick={() => history.push("deposit")}><ListItemText>Deposit</ListItemText></ListItem>
                                                                    <ListItem
                                                                        onClick={() => history.push("withdraw")}><ListItemText>Withdraw</ListItemText></ListItem>
                                                                    <ListItem
                                                                        onClick={() => history.push("Trade?pair=btcusdt")}><ListItemText>Exchange</ListItemText></ListItem>
                                                                </ul>
                                                            </td>
                                                        </tr>
                                                    )
                                            )
                                        ))}
                                        </tbody>
                                    </table>
                                    <section className='walletTableFeedback'>{content}</section>
                                </div>
                            </div>
                        </div>
                        {/*            <div class="card Financial History">*/}
                        {/*                <div class="card-header ">*/}
                        {/*                    Financial Asset*/}
                        {/*                    <div class="form-">*/}
                        {/*                 */}
                        {/*                    </div>*/}
                        {/*                </div>*/}
                        {/*                <div class="card-body">*/}
                        {/*                    <div class="tab-pane  crypto-table" id="history-table" role="tabpanel"*/}
                        {/*                         aria-labelledby="pills-home-tab">*/}
                        {/*                        <table class="table bg-transparent text-center ">*/}
                        {/*                            <thead>*/}
                        {/*                            <tr class="crypto-property">*/}
                        {/*                                <th scope="col">Name <span><i class="fas fa-sort"></i></span></th>*/}
                        {/*                                <th scope="col">Coin Type<span><i class="fas fa-sort"></i></span></th>*/}
                        {/*                                <th scope="col">Operation <span><i class="fas fa-sort"></i></span></th>*/}
                        {/*                                <th scope="col">Amount<span><i class="fas fa-sort"></i></span></th>*/}
                        {/*                                <th scope="col">status</th>*/}
                        {/*                            </tr>*/}
                        {/*                            </thead>*/}
                        {/*                            <tbody>*/}
                        {/*                            </tbody>*/}
                        {/*                        </table>*/}
                        {/*                    </div>*/}
                        {/*                </div>*/}
                        {/*            </div>*/}
                    </div>
                </div>
            </div>

            <br/>
            <br/>
            <Footer/>
        </div>
    );
}
