import React, {useCallback, useEffect, useState} from 'react';
import Slider from 'react-animated-slider';
import verticalCss from '../../component/css/vertical.scss';
import axios from "axios";
import {CircularProgress} from "@material-ui/core";

const VerticalSlider = () => {


    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);
    const [tickers, setTickers] = useState([]);

    const fetchCoinHandler = useCallback(async () => {
        setIsLoading(true);
        setError(null);
        try {

            const listResponse = await axios.get('currencies/list');

            if (listResponse.status !== 200) {
                throw new Error('Something went wrong with CoinList!');
            }

            const marketL = (listResponse.data);

            const loadedList = [];


            for (var keys in marketL) {
                loadedList.push({
                    name: marketL[keys].markets,
                    rank: marketL[keys].rank
                });
            }


            const response = await axios.get('core/marketTickerAll');

            if (response.status !== 200) {
                throw new Error('Something went wrong!');
            }

            const data = await (response.data.data.ticker);

            const loadedData = [];
            const loadedTL = [];
            const loadedVL = [];

            for (var i = 0; i <= loadedList.length; i++) {
                for (var key in data) {
                    if (loadedList[i] != undefined && loadedList[i].name != undefined && key.toLowerCase() == loadedList[i].name[0].toLowerCase()) {
                        loadedData.push({
                            id: loadedList[i].rank,
                            name: key,
                            lastPrice: data[key].last,
                            dayOpen: data[key].open,
                            volume: parseFloat(data[key].vol).toFixed(2),
                            value: (data[key].vol * data[key].last).toFixed(2),
                            DayChange: (((data[key].last - data[key].open)/data[key].open)*100).toFixed(2)
                        });
                    }
                }
            }

            loadedData.sort(function (a,b){return b.value - a.value});
            setTickers(loadedData);

        } catch (error) {
            console.log(error);
            setError(error.message);
        }
        setIsLoading(false);
    }, []);

    useEffect(() => {
        fetchCoinHandler();
    }, [fetchCoinHandler]);

    let content;

    if (tickers.length === 0) {
        content = <p>Found no Coin.</p>;
    }

    if (error) {
        content = <p>{error}</p>;
    }

    if (isLoading) {
        content = <p><CircularProgress /></p>;
    }

    return (
        <div>
            {content}
                <Slider classNames={verticalCss} direction="vertical" autoplay="3000" >

                        <div>
                            <div className="center">
                                <ul className="list-group list-group-horizontal justify-content-center">
                                    {tickers.slice(0 , 6).map((item) => (
                                        <div className="list-group-item priceList ">
                                            <h5 className='sliderPercent d-inline'>{item.name.split("USDT")[0]}<section className="d-inline">/USDT</section> <span className={`${item.DayChange > 0 ? 'inGrow' : 'inLose'}`}>{item.DayChange > 0 && '+'}{item.DayChange}%</span></h5>
                                            <p className="exact-price">{item.lastPrice} $</p>
                                            {/*<span className="price">{item.value}$</span>*/}
                                        </div>
                                    ))}
                                </ul>
                            </div>
                        </div>

                    <div>
                        <div className="center">
                            <ul className="list-group list-group-horizontal justify-content-center">
                                {tickers.slice(6 , 12).map((item) => (
                                    <div className="list-group-item priceList">
                                        <h5 className='sliderPercent d-inline'>{item.name.split("USDT")[0]}<section className="d-inline">/USDT</section> <span className={`${item.DayChange > 0 ? 'inGrow' : 'inLose'}`}>{item.DayChange > 0 && '+'}{item.DayChange}%</span></h5>
                                        <p className="exact-price">{item.lastPrice} $</p>
                                        {/*<span className="price">{item.value}$</span>*/}
                                    </div>
                                ))}
                            </ul>
                        </div>
                    </div>

                    <div>
                        <div className="center">
                            <ul className="list-group list-group-horizontal justify-content-center">
                                {tickers.slice(0 , 6).map((item) => (
                                    <div className="list-group-item priceList">
                                        <h5 className='sliderPercent d-inline'>{item.name.split("USDT")[0]}<section className="d-inline">/USDT</section> <span className={`${item.DayChange > 0 ? 'inGrow' : 'inLose'}`}>{item.DayChange > 0 && '+'}{item.DayChange}%</span></h5>
                                        <p className="exact-price">{item.lastPrice} $</p>
                                        {/*<span className="price">{item.value}$</span>*/}
                                    </div>
                                ))}
                            </ul>
                        </div>
                    </div>

                    <div>
                        <div className="center">
                            <ul className="list-group list-group-horizontal justify-content-center">
                                {tickers.slice(6 , 12).map((item) => (
                                    <div className="list-group-item priceList">
                                        <h5 className='sliderPercent d-inline'>{item.name.split("USDT")[0]}<section className="d-inline">/USDT</section> <span className={`${item.DayChange > 0 ? 'inGrow' : 'inLose'}`}>{item.DayChange > 0 && '+'}{item.DayChange}%</span></h5>
                                        <p className="exact-price">{item.lastPrice} $</p>
                                        {/*<span className="price">{item.value}$</span>*/}
                                    </div>
                                ))}
                            </ul>
                        </div>
                    </div>

                </Slider>

        </div>
    );
};

export default VerticalSlider;
