            setTickers(loadedData);
            loadedTL.sort(function (a,b){return a.DayChange - b.DayChange});
            setTopLosers(loadedTL);
            loadedVL.sort(function (a,b){return b.value - a.value});
            setVLeaders(loadedVL);

