import React, {useContext, useEffect, useState} from "react";
import '../css/bootstrap.css';
import '../css/messageCenter.css';
import NavBar from "../widgets/NavBar";
import Footer from "../widgets/Footer";
import { MdMoveToInbox } from "@react-icons/all-files/md/MdMoveToInbox";
import {FormattedMessage} from "react-intl";
import Trail from "../widgets/Trail";
import {Context} from "../../translation/Wrapper"

import AccountsNavigator from "../widgets/AccountsNavigator";
import {LoginContext} from "../../Root";
import {useHistory} from "react-router-dom";

export default function MessagesScreen() {
  const history = useHistory();
  const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
  useEffect(() => {
    if(!isLoggedIn)
      history.push('login');
  }, []);
  const [open, set] = useState(true);
  const context =useContext(Context);
  return (
      <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
        <NavBar />
        <br/>
        <br/>
        <br/>
        <br/>
      <div className="container-fluid ">
    <div className="row justify-content-center">
      <div className="col-md-12 col-lg-12 col-xl-10  col-sm-12 col-xs-12">
        <div className="card shadow messagesBox" >
          <div className="card-header">

            <AccountsNavigator />

            <h2 className="card-title inbox-title m-3" dir={context.locale == "fa" && "rtl"}>
              {" "}
              <FormattedMessage
                  id="MessageCenter.Title"
                  defaultMessage="InBox"
              />{" "}
              <span className="messageIco">
              <MdMoveToInbox className="fas fa-inbox " />
            </span>
            </h2>
            <div className="row justify-content-end" dir={context.locale == "fa" && "rtl"}>
              <div className="deleteAndSelectAll-container">
                <button type="button" className="btn deleteAll">
                  <i className="fas fa-trash-alt" />
                  <FormattedMessage
                      id="MessageCenter.DeleteAll"
                      defaultMessage="DeleteAll"
                  />
                </button>
                <button type="button" className="btn selectAll">
                  <i className="fas fa-envelope-open-text" />
                  <FormattedMessage
                      id="MessageCenter.ReadAll"
                      defaultMessage="ReadAll"
                  />
                </button>
              </div>
            </div>
            <ul className="nav nav-tabs card-header-tabs messagesTabs" dir={context.locale == "fa" && "rtl"}>
              <li className="nav-item all">
                <a className="nav-link active" aria-current="true" href="#">
                  <FormattedMessage
                      id="MessageCenter.All"
                      defaultMessage="All"
                  />{" "}
                </a>
              </li>
              <li className="nav-item readed">
                <a className="nav-link" href="#">
                  <FormattedMessage
                      id="MessageCenter.readed"
                      defaultMessage="readed"
                  />
                </a>
              </li>
              <li className="nav-item unread">
                <a
                    className="nav-link "
                    href="#"
                    tabIndex={-1}
                    aria-disabled="true"
                >
                  <FormattedMessage
                      id="MessageCenter.unread"
                      defaultMessage="unread"
                  />
                </a>
              </li>
            </ul>
          </div>
          <div className="card-body">
            <ul className="list-group messagesContainer list-group-flush" dir={context.locale == "fa" && "rtl"}>
              <li className="list-group-item " >
                <h6 className="passageTitle" >
                  Hey Title <br />
                  <p className="passage">Lorem dolor amet sit</p>
                </h6>
                <small>3 days ago</small>
              </li>
              <li className="list-group-item " onClick={() => set(state => !state)}>
                <h6 className="passageTitle">
                  Hey Title <br />
                  <Trail open={open} >
                  <p className="passage">
                    By default, jQuery comes with queue functionality for
                    animations. This means that if you write multiple animate()
                    calls after each other, jQuery creates an "internal" queue
                    with these method calls. Then it runs the animate calls ONE by
                    ONE. So, if you want to perform different animations after
                    each other, we take advantage of the queue functionality:
                  </p>
                    <br/>
                  </Trail>
                </h6>
                <small>3 days ago</small>
              </li>
              {/*{items.map(item => (*/}
              {/*    <li key={item.id}>{item.name}</li>*/}

              {/*  <li key={item.id} className="list-group-item " onClick={() => set(state => !state)}>*/}
              {/*  <h6 className="passageTitle">*/}
              {/*  Hey Title <br />*/}
              {/*  <Trail open={open} >*/}
              {/*  <p className="passage">*/}
              {/*  By default, jQuery comes with queue functionality for*/}
              {/*  animations. This means that if you write multiple animate()*/}
              {/*  calls after each other, jQuery creates an "internal" queue*/}
              {/*  with these method calls. Then it runs the animate calls ONE by*/}
              {/*  ONE. So, if you want to perform different animations after*/}
              {/*  each other, we take advantage of the queue functionality:*/}
              {/*  </p>*/}
              {/*  <br/>*/}
              {/*  </Trail>*/}
              {/*  </h6>*/}
              {/*  <small>3 days ago</small>*/}
              {/*  </li>*/}
              {/*))}*/}
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
        <br/>
        <br/>
        <Footer />
      </div>
  );
}
