import React, {createContext, useState} from "react";
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import App from "./App";
import SocketContext from "./SocketContext";
import LoginAndSignup from "./component/LoginAndSignup/LoginAndSignup";
import Account from "./component/accounts/Account";
import AccountLevel from "./component/accounts/AccountLevel";
import Withdraw from "./component/market/Withdraw";
import Trade from "./component/market/Trade";
import CoinInfoScreen from "./component/coins/CoinInfoScreen";
import DepositScreen from "./component/market/DepositScreen";
import FAQScreen from "./component/more_pages/FAQScreen";
import IdVerification from "./component/accounts/IdVerification";
import MarketList from "./component/market/MarketList";
import MessagesScreen from "./component/accounts/MessagesScreen";
import SettingScreen from "./component/accounts/SettingScreen";
import VerifiedScreen from "./component/widgets/VerifiedScreen";
import WalletOverview from "./component/accounts/WalletOverview";
import Convert from "./component/market/Convert";
import {create} from "jss";
import rtl from "jss-rtl";
import {StylesProvider, jssPreset} from "@material-ui/core/styles";
import NotAllowed from "./component/widgets/NotAllowed";
import ForgetPassword from "./component/LoginAndSignup/ForgetPassword";


const jss = create({plugins: [...jssPreset().plugins, rtl()]});

export const LoginContext = createContext();
export const VerificationContext = createContext();


const Root = () => {

    const [isLoggedIn, setIsLoggedIn] = useState(
        localStorage.getItem("token") != null
    );

    const [isVerified, setIsVerified] = useState(
        localStorage.getItem("verified") != null &&
        localStorage.getItem("verified") == "true"
    );

    localStorage.removeItem("tempToken");
    localStorage.removeItem("username");


    return (
        <StylesProvider jss={jss}>
            <LoginContext.Provider value={{isLoggedIn, setIsLoggedIn}}>
                <VerificationContext.Provider value={{isVerified, setIsVerified}}>
                    <SocketContext.Consumer>

                        {(value) => {
                            return (
                                <Router>
                                    <Switch>
                                        <Route
                                            component={(props) => <App socket={value} {...props} />}
                                            exact
                                            path="/"
                                        />
                                        <Route
                                            component={(props) => (
                                                <LoginAndSignup isSignup={false} socket={value} {...props} />
                                            )}
                                            exact
                                            path="/login"
                                        />
                                        <Route
                                            component={(props) => (
                                                <LoginAndSignup isSignup={true} socket={value} {...props} />
                                            )}
                                            exact
                                            path="/signup"
                                        />
                                        <Route
                                            component={(props) => (
                                                <ForgetPassword socket={value} {...props} />
                                            )}
                                            exact
                                            path="/forgetPassword"
                                        />

                                        <Route
                                            component={(props) => <NotAllowed socket={value} {...props} />}
                                            exact
                                            path="/account"
                                        />
                                        <Route
                                            component={(props) => (
                                                <AccountLevel socket={value} {...props} />
                                            )}
                                            exact
                                            path="/accountLevel"
                                        />
                                        <Route
                                            component={(props) => <Trade socket={value} {...props} />}
                                            exact
                                            path="/trade"
                                        />
                                        <Route
                                            component={(props) => (
                                                <Withdraw socket={value} {...props} />
                                            )}
                                            exact
                                            path="/withdraw"
                                        />
                                        <Route
                                            component={(props) => (
                                                <CoinInfoScreen socket={value} {...props} />
                                            )}
                                            exact
                                            path="/coin_info"
                                        />
                                        <Route
                                            component={(props) => (
                                                <DepositScreen socket={value} {...props} />
                                            )}
                                            exact
                                            path="/deposit"
                                        />
                                        <Route
                                            component={(props) => (
                                                <Convert socket={value} {...props} />
                                            )}
                                            exact
                                            path="/Convert"
                                        />
                                        <Route
                                            component={(props) => (
                                                <FAQScreen socket={value} {...props} />
                                            )}
                                            exact
                                            path="/faq"
                                        />
                                        <Route
                                            component={(props) => (
                                                <IdVerification socket={value} {...props} />
                                            )}
                                            exact
                                            path="/id_verification"
                                        />
                                        <Route
                                            component={(props) => (
                                                <MarketList socket={value} {...props} />
                                            )}
                                            exact
                                            path="/markets"
                                        />
                                        <Route
                                            component={(props) => (
                                                <MessagesScreen socket={value} {...props} />
                                            )}
                                            exact
                                            path="/messages"
                                        />
                                        <Route
                                            component={(props) => (
                                                <SettingScreen socket={value} {...props} />
                                            )}
                                            exact
                                            path="/settings"
                                        />
                                        <Route
                                            component={(props) => (
                                                <VerifiedScreen socket={value} {...props} />
                                            )}
                                            exact
                                            path="/verified"
                                        />
                                        <Route
                                            component={(props) => (
                                                <WalletOverview socket={value} {...props} />
                                            )}
                                            exact
                                            path="/wallet_overview"
                                        />
                                    </Switch>
                                </Router>
                            );
                        }}

                    </SocketContext.Consumer>
                </VerificationContext.Provider>
            </LoginContext.Provider>
        </StylesProvider>
    );
};

export default Root;
