import React, {useContext, useState} from "react";
import "../css/bootstrap.css";
import "../css/NavBar.scss";
import {CgDarkMode} from "@react-icons/all-files/cg/CgDarkMode";
import {RiAccountPinBoxLine} from "@react-icons/all-files/ri/RiAccountPinBoxLine";
import {IoMdSettings} from "@react-icons/all-files/io/IoMdSettings";
import TranslateOutlinedIcon from '@mui/icons-material/TranslateOutlined';
import {FaCheck} from "@react-icons/all-files/fa/FaCheck";
import {BsBellFill} from "@react-icons/all-files/bs/BsBellFill";
import {FaBars} from "@react-icons/all-files/fa/FaBars";
import {FaWallet} from "@react-icons/all-files/fa/FaWallet";
import {LoginContext} from "../../Root";
import {useHistory} from "react-router-dom";
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper";
import {Theme} from "../ThemeContext";
import {Avatar, Badge, Divider, ListItem, ListItemAvatar, ListItemText, Typography} from "@material-ui/core";
import {RiLogoutBoxRLine} from "@react-icons/all-files/ri/RiLogoutBoxRLine";
import pic from "../css/photo_2021-06-30_11-38-49.jpg";
import List from "@material-ui/core/List";
import IconButton from "@material-ui/core/IconButton";
import LightModeOutlinedIcon from '@mui/icons-material/LightModeOutlined';
import axios from "axios";

const NavBar = (props) => {
    const [toggle, setToggle] = useState(false);
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
    const history = useHistory();
    const context = useContext(Context);
    const theme = useContext(Theme);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [languageOpen, setLanguageOpen] = useState(false);

    const logOut = async () => {
        setIsLoading(true)
        setError(null);
        try {
            const response = await axios.get('auth/logout' , {
                headers:{
                    'Authorization': "Bearer " + localStorage.getItem("token")
                }
            });
            if (response.status !== 200) {
                throw new Error('Something went wrong!');
            }
            localStorage.removeItem("token");

            refreshPage();
        } catch (error) {
            setError(error.message);
        }
    }

    const refreshPage = () => {
        window.location.reload();
    }

    return (
        <nav
            className={`navbar navbar-expand-lg fixed-top ${context.locale == "fa" && "font-face-farsi"} ${props.isOpen && 'd-none'} `}
            id="navMenu">
            <div className="container-fluid ps-3" id="navbar-C">
                <a
                    onClick={() => {
                        history.push("/");
                        refreshPage()
                    }}
                    className="navbar-brand dark-them" href="#">
                    <FormattedMessage
                        id="NavBar.Title"
                        defaultMessage="Sigloy"
                    />
                </a>
                {
                    !isLoggedIn &&
                    <button type="button" onClick={() => history.push("login")} className={`btn`} id="hidden-started">
                        <FormattedMessage
                            id="NavBar.GetStart"
                            defaultMessage="Get Start"
                        />
                    </button>
                }


                <div className="d-flex">
                    <li className={`nav-item Nav-menu secLan`}>
                        <button className="Nav-menu-title languageIco-hidden  btn" aria-controls="simple-menu"
                                aria-haspopup="true">
                            <TranslateOutlinedIcon className="fas fa-id-badge"/>{" "}
                        </button>

                        <ul className="Nav-menu-dropdown language-dropdown">
                            <ListItem
                                onClick={() => context.selectLanguage('en')}><ListItemText> English {context.locale === 'en' &&
                            <span className='checked'><FaCheck/></span>}</ListItemText></ListItem>
                            <ListItem
                                onClick={() => context.selectLanguage('fa')}><ListItemText>  {context.locale === 'fa' &&
                            <span className='checked'><FaCheck/></span>}</ListItemText></ListItem>

                        </ul>
                    </li>
                    <span className="DarkMode-hidden" onClick={theme.themeChanger}>
                        <a className="nav-link DarkMode dark-them">
                            <LightModeOutlinedIcon className="fas fa-adjust"/>
                        </a>
                    </span>
                    <button
                        className="navbar-toggler "
                        type="button"
                        onClick={() => {
                            setToggle(!toggle);
                        }}
                        data-bs-toggle="collapse"
                        data-bs-target="#navbarToggleExternalContent"
                        aria-controls="navbarToggleExternalContent"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
          <span className="btn dark-them btn-outline-light">
            <FaBars className="fas fa-bars"/>
          </span>
                    </button>
                </div>
                <div className={`collapse navbar-collapse ${toggle && "d-block"}`}>
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0 start-items">
                        {/*<li className="nav-item ">*/}
                        {/*    <a*/}

                        {/*        className="nav-link dark-them" href="#">*/}
                        {/*        <FormattedMessage*/}
                        {/*            id="NavBar.BuyCrypto"*/}
                        {/*            defaultMessage="Buy Crypto"*/}
                        {/*        />*/}
                        {/*    </a>*/}
                        {/*</li>*/}

                        <li className={`nav-item `}>
                            <a onClick={() => history.push("markets")}
                               className="nav-link dark-them" href="#">
                                <FormattedMessage
                                    id="NavBar.Market"
                                    defaultMessage="Market"
                                />
                            </a>
                        </li>


                        <li className={`nav-item `}>
                            <a onClick={() => history.push("Trade?pair=btcusdt")}
                               className="nav-link dark-them" href="#">
                                <FormattedMessage
                                    id="NavBar.Trade"
                                    defaultMessage="Trade"
                                />
                            </a>
                        </li>

                        <li className={`nav-item ${!isLoggedIn && "d-none"}`}>
                            <a onClick={() => history.push("Convert")}
                               className="nav-link dark-them" href="#">
                                {/*<FormattedMessage*/}
                                {/*    id="NavBar.Trade"*/}
                                {/*    defaultMessage="Trade"*/}
                                {/*/>*/}
                                Convert
                            </a>
                        </li>


                        {/*<li className="nav-item  ">*/}
                        {/*    <a className="nav-link dark-them" href="#">*/}
                        {/*        <FormattedMessage*/}
                        {/*            id="NavBar.Finance"*/}
                        {/*            defaultMessage="Finance"*/}
                        {/*        />*/}
                        {/*    </a>*/}
                        {/*</li>*/}
                        {/*<li className="nav-item  ">*/}
                        {/*    <a className="nav-link dark-them" href="#">*/}
                        {/*        NFT*/}
                        {/*    </a>*/}
                        {/*</li>*/}
                    </ul>
                    <span className=" navbar-text ">
            <div className>
              <ul className="navbar-nav end-items mt-1" id="first-nav">
                <li className={`nav-item Nav-menu ${!isLoggedIn && "d-none"}`}>
                         <button className="Nav-menu-title walletIco btn" aria-controls="simple-menu"
                                 aria-haspopup="true">
                    <FaWallet className="fas fa-id-badge"/>{" "}
                         </button>

                      <ul className="Nav-menu-dropdown walletDropdown">
                                <ListItem
                                    onClick={() => history.push("wallet_overview")}><ListItemText>Wallet OverView</ListItemText></ListItem>
                                <ListItem onClick={() => history.push("withdraw")}><ListItemText>Withdraw</ListItemText></ListItem>
                                <ListItem
                                    onClick={() => history.push("deposit")}><ListItemText>Deposit</ListItemText></ListItem>
                      </ul>

                </li>

                      <li className={`nav-item Nav-menu ${!isLoggedIn && "d-none"}`}>
                            <button className="Nav-menu-title btn" aria-controls="simple-menu"
                                    aria-haspopup="true">
                                <RiAccountPinBoxLine className="fas fa-id-badge"/>
                            </button>
                          {/*<a onClick={() => history.push("withdraw")}*/}
                          {/*   className="nav-link bell dark-them m-0" href="#">*/}
                          {/*    <FormattedMessage*/}
                          {/*        id="NavBar.Withdraw"*/}
                          {/*        defaultMessage="Finance"*/}
                          {/*    />{" "}*/}
                          {/*</a>*/}

                          <ul className="Nav-menu-dropdown">

                                 <ListItem alignItems="flex-start">

        <ListItemAvatar>
          <Avatar style={{width: 60, height: 60}} alt="AmirAslani" src={pic}/>
        </ListItemAvatar>

                     <IconButton style={{textAlign: "left"}} disabled={true}>
                                     <Badge badgeContent={'VIP3'} color="secondary">
        <ListItemText
            secondary={
                <React.Fragment>
                    <Typography
                        component="div"
                        variant="body2"
                        color="text.primary"
                    >
                        Amir Aslani
                    </Typography>
                    <Typography
                        component="div"
                        variant="body2"
                        color="text.primary"
                    >
                        a.aslani99j@gmail.com
                    </Typography>

                </React.Fragment>
            }
        />
                                     </Badge>
                     </IconButton>
      </ListItem>

      <Divider variant="inset" component="li"/>
                              <li style={{textAlign: "left"}} onClick={logOut}><ListItemText>Logout <RiLogoutBoxRLine/> </ListItemText></li>
                            </ul>
                        </li>

                           <li className={`nav-item Nav-menu ${!isLoggedIn && "d-none"}`}>
          <button className="Nav-menu-title walletIco btn" aria-controls="simple-menu"
                  aria-haspopup="true">
                    <IoMdSettings className="fas fa-id-badge"/>{" "}
          </button>

                                    <ul className="Nav-menu-dropdown account-dropdown">
                                <ListItem onClick={() => history.push("settings")}><ListItemText>Settings</ListItemText></ListItem>
                                <ListItem
                                    onClick={() => history.push("account")}><ListItemText>Account</ListItemText></ListItem>
                                <ListItem
                                    onClick={() => history.push("accountLevel")}><ListItemText>Account Level</ListItemText></ListItem>

                      </ul>
                </li>



                <li className={`nav-item ${!isLoggedIn && "d-none"}`}>
                  <a onClick={() => history.push("messages")}
                     className="nav-link bell dark-them m-0" href="#">
                         <Badge badgeContent={4} color="secondary">
      <BsBellFill className="fas fa-bell"/>
    </Badge>{" "}
                  </a>
                </li>
                <li className={`nav-item ${isLoggedIn && "d-none"} `}>
                  <button
                      onClick={() => history.push("login")}
                      type="button" className="btn" id="started">
                         <FormattedMessage
                             id="NavBar.GetStart"
                             defaultMessage="Get Start"
                         />
                  </button>
                </li>

                                       <li className={`nav-item Nav-menu languageChanger`}>
          <button className="Nav-menu-title languageIco btn" aria-controls="simple-menu"
                  aria-haspopup="true">
                    <TranslateOutlinedIcon className="fas fa-id-badge"/>{" "}
          </button>

                                    <ul className="Nav-menu-dropdown language-dropdown">
                                <ListItem
                                    onClick={() => context.selectLanguage('en')}><ListItemText> English {context.locale === 'en' &&
                                <span className='checked'><FaCheck/></span>}</ListItemText></ListItem>
                                <ListItem
                                    onClick={() => context.selectLanguage('fa')}><ListItemText>  {context.locale === 'fa' &&
                                <span className='checked'><FaCheck/></span>}</ListItemText></ListItem>

                      </ul>
                </li>

                  <li className="nav-item pe-3 ps-3 darkThemAll" onClick={theme.themeChanger}>
                  <a
                      className="nav-link DarkMode dark-them">
                    <LightModeOutlinedIcon className="fas fa-adjust"/>
                  </a>
                </li>
              </ul>
            </div>
          </span>
                </div>
            </div>
        </nav>
    );
};

export default NavBar;
