import React, {useCallback, useContext, useEffect, useRef, useState} from 'react';
import NavBar from "../widgets/NavBar";
import "../css/withdraw.css";
import "../css/bootstrap.css";
import {FaSearchDollar} from "@react-icons/all-files/fa/FaSearchDollar";
import {buildStyles, CircularProgressbar} from "react-circular-progressbar";
import Axios from "axios";
import LoadingDialog from "../widgets/LoadingDialog";
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper";
import {useHistory} from "react-router-dom";
import {LoginContext} from "../../Root";
import MarketSearchSec from "../widgets/MarketSearchSec";
import {CircularProgress} from "@material-ui/core";
import axios from "axios";

const Withdraw = () => {

    const percentage = 50;

    const [Address, setAddress] = useState();
    const [Arrived, setArrived] = useState("");
    const [arrivedType, setArrivedType] = useState("");
    const [selectedNetworks, setNetworks] = useState();
    const [selectedFee, setFee] = useState();

    const [all, setAll] = useState();


    const [selectedOne, setSelectedOne] = useState();
    const [configs, setConfigs] = useState();
    const [chains, setChain] = useState([]);
    const [coinBalanceInfo, setCoinBalanceInfo] = useState([]);

    const [isLoading, setLoading] = useState(false);
    const [IsLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);

    const AddressRef = useRef("");
    const ArrivedRef = useRef("");

    const history = useHistory();
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);

    const fetchGeneralList = useCallback(async () => {
        setIsLoading(true);
        setError(null);
        try {
            const response = await fetch('/v1/common/asset/config');

            if (response.status !== 200) {
                throw new Error('Something went wrong!');
            }

            const generalLi = await response.json();
            // const data = await (response.data.data.ticker);


            const loadedData = [];


            for (var key in generalLi.data) {
                loadedData.push({
                    name: generalLi.data[key].asset,
                    withdraw_least_amount: generalLi.data[key].withdraw_least_amount,
                    withdraw_tx_fee: generalLi.data[key].withdraw_tx_fee,
                    chain: generalLi.data[key].chain,
                    can_withdraw: generalLi.data[key].can_withdraw
                });
            }

            setConfigs(loadedData);

        } catch (error) {
            setError(error.message);
        }
        setIsLoading(false);
    }, []);

    useEffect(() => {
        if (!isLoggedIn) {
            history.push('login');
        }
        else {
            fetchGeneralList();
        }
    }, [fetchGeneralList]);

    const onSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        await Axios.post("", {
            Address: AddressRef.current,
            Arrived: ArrivedRef.current,
        })
            .then((res) => {
                if (res.data.status == "ok") {

                    console.log(res.data.data.tmp_token);

                } else {

                }
            })
            .catch((err) => {
            });
        setLoading(false);
    };

    const fetchCoinData = useCallback(async (val, networks, config) => {
        setIsLoading(true);
        setArrived("");
        setSelectedOne(null);
        setArrivedType(val);
        const loadedConfigs = [];
        for (var key in networks) {
            if (networks[key].name === val) {
                setNetworks(networks[key]);
            }
            for (var i in config) {
                for (var j in networks[key].networks) {
                    if (networks[key].name === val && config[i].name === val && networks[key].networks[j] == config[i].chain) {
                        loadedConfigs.push({
                            name: val,
                            network: networks[key].networks[j],
                            chain: config[i].chain,
                            withdraw_tx_fee: config[i].withdraw_tx_fee
                        })

                    }
                }
            }
        }

        setChain(loadedConfigs);

        const Response = await axios.get('core/balanceInfo' , {
            headers:{
                'Authorization': "Bearer " + localStorage.getItem("token")
            }
        });

        if (Response.status !== 200) {
            throw new Error('Something went wrong with CoinList!');
        }

        const balanceInfo = (Response.data.data);

        const loadedBalanceInfo = [];

        for (var balance in balanceInfo) {
            if (balance == val) {
                loadedBalanceInfo.push({
                    name: val,
                    available: balanceInfo[balance].available,
                    frozen: balanceInfo[balance].frozen,
                })
            }
        }

        setCoinBalanceInfo(loadedBalanceInfo);

        setIsLoading(false);
    }, []);


    const context = useContext(Context);

    let content;

    if (chains.length === 0 && selectedNetworks != undefined) {
        content = <li className="list-group-item border-0">Found no Chain.</li>;
    }

    if (error) {
        content = <li className="list-group-item border-0">{error}</li>;
    }

    if (IsLoading) {
        content = <li className="list-group-item border-0"><CircularProgress/></li>;
    }


    return (
        <div>
            <NavBar/>
            {isLoading && <LoadingDialog/>}
            <br/>
            <br/>
            <br/>
            <br/>
            <div className="container-fluid ">
                <div className="row justify-content-center">
                    <div className="col-md-10 col-lg-10 col-xl-8 col-xxl-8 col-sm-12 col-xs-12">
                        <div className="card shadow">
                            <div className="card-header">
                                <div className="Deposit-title-container ms-1" dir={context.locale == "fa" && "rtl"}>
                                    <p className="title m-0">
                                        <FormattedMessage
                                            id="Withdraw.Title"
                                            defaultMessage="Withdraw"
                                        />
                                    </p>
                                </div>
                            </div>
                            <div className="card-body">
                                <div className="row">
                                    <div className="col-lg-8 col-xl-8 col-sm-12 col-xs-12">
                                        <div className="card">
                                            <div className="card-header border-0" dir={context.locale == "fa" && "rtl"}>
                                                <label className="fw-bold">Coin</label>
                                                <MarketSearchSec
                                                    configData={configs}
                                                    onFetchCoinData={fetchCoinData}
                                                    lable={<FormattedMessage
                                                        id="Withdraw.Coin"
                                                        defaultMessage="Coin"
                                                    />}/>

                                                <div className="Assets-container-responsive mt-4"
                                                     dir={context.locale == "fa" && "rtl"}>
                                                    {coinBalanceInfo.map((Balance) => (
                                                        <div className="row justify-content-around">
                                                            <p className="assets">
                                                                <FormattedMessage
                                                                    id="Withdraw.Assets"
                                                                    defaultMessage="Assets"
                                                                />
                                                                <br/>
                                                                <span
                                                                    className="Amount">{Math.floor((parseFloat(Balance.available) + parseFloat(Balance.frozen)) * 1000000) / 1000000} {Balance.name}</span>
                                                            </p>
                                                            <p className="frozen-assets">
                                                                <FormattedMessage
                                                                    id="Withdraw.FrozenAsset"
                                                                    defaultMessage="FrozenAsset"
                                                                />
                                                                <br/>
                                                                <span
                                                                    className="Amount">{Math.floor(Balance.frozen * 1000000) / 1000000} {Balance.name}</span>
                                                            </p>
                                                            <p className="available">
                                                                <FormattedMessage
                                                                    id="Withdraw.Available"
                                                                    defaultMessage="Available"
                                                                />
                                                                <br/>
                                                                <span
                                                                    className="Amount">{Math.floor(Balance.available * 1000000) / 1000000} {Balance.name}</span>
                                                            </p>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                            <div className="card-body">
                                                <div className="withdrawProtocols">
                                                    <h5 className="Protocol-Type mt-3 mb-4"
                                                        dir={context.locale == "fa" && "rtl"}>
                                                        <FormattedMessage
                                                            id="Deposit.ProtocolType"
                                                            defaultMessage="ProtocolType"
                                                        />
                                                    </h5>

                                                    <ul className="list-group list-group-horizontal-md withdrawProtocol-list ">
                                                        {chains.map((chains, index) => (
                                                            <li className="list-group-item col-3">
                                                                <a className="col-12" href="#">
                                                                    <button onClick={() => {setSelectedOne(index);setFee(chains.withdraw_tx_fee)}}
                                                                            className={`${selectedOne == index ? 'protocolBtn-selected' : 'protocolBtn'}`}>
                                                                        {chains.network}
                                                                        <br/>
                                                                        <span className="fee-container">
                                                          <div
                                                              className="fee-amount">Fee : {chains.withdraw_tx_fee}</div>
                                                                            {" "}
                                                                            {chains.name}
                                                        </span>
                                                                    </button>
                                                                </a>
                                                            </li>
                                                        ))}
                                                    </ul>
                                                    <section
                                                        className='card-text  text-center protocol-feedback'>{content}</section>
                                                </div>
                                                <div className="Completion">
                                                    <form>
                                                        <div className="row justify-content-center m-3">
                                                            <div className="withdraw-address-input-container mt-4 mb-2">
                                                                <h5 className="form-label Withdrawal-Address mt-3"
                                                                    dir={context.locale == "fa" && "rtl"}>
                                                                    <FormattedMessage
                                                                        id="Withdraw.WithdrawalAddress"
                                                                        defaultMessage="WithdrawalAddress"
                                                                    />
                                                                </h5>
                                                                <div className="input-group mb-3">
                                                                    <input
                                                                        type="text"
                                                                        className="form-control"
                                                                        placeholder="Recipient's username"
                                                                        aria-label="Recipient's username"
                                                                        aria-describedby="button-addon2"
                                                                        ref={AddressRef}
                                                                    />
                                                                </div>
                                                                {/*                                                    <span class="t-text d-flex">*/}
                                                                {/*                                                    <p class="Withdrawable-Amount-container pe-2">*/}
                                                                {/*                                                         Withdrawable Amount:<p class="Withdrawable-Amount pe-2">0<span*/}
                                                                {/*                                                            class="crypto-type ps-2">BTC</span></p>*/}
                                                                {/*                                                        </p>*/}
                                                                {/*                                                   </span>*/}
                                                                <h5 className="form-label Actual-Arrived mt-5"
                                                                    dir={context.locale == "fa" && "rtl"}>
                                                                    <FormattedMessage
                                                                        id="Withdraw.ActualArrived"
                                                                        defaultMessage="ActualArrived"
                                                                    />
                                                                </h5>
                                                                <div className="input-group Actual-Arrived mb-3">
                                                                    <input
                                                                        type="text"
                                                                        className="form-control border-end-0"
                                                                        placeholder="Recipient's username"
                                                                        aria-label="Recipient's username"
                                                                        aria-describedby="button-addon2"
                                                                        ref={ArrivedRef}
                                                                        value={Arrived}
                                                                        onChange={(e) =>{setArrived(e.target.value)}}
                                                                    />
                                                                    <span className="e-button-container">
                              {arrivedType}
                                                                        <button
                                                                            className="e-button btn btn-outline-secondary"
                                                                            disabled={selectedOne != null ? false : true}
                                                                            type="button" onClick={() => {setArrived(coinBalanceInfo[0].available)
                                                                        }}>All</button>
                            </span>
                                                                </div>
                                                                <h5 className="withdraw-amount-title mt-4 mb-4">
                                                                    <FormattedMessage
                                                                        id="Withdraw.Limit"
                                                                        defaultMessage="WithdrawLimit"
                                                                    />
                                                                    <span className="withdraw-amount-sec">250/500</span>
                                                                </h5>
                                                            </div>
                                                        </div>
                                                        <a href="/">
                                                            <button
                                                                className={`bn632-hover bn25 ${context.locale == "fa" ? "float-start" : "float-end"}  col-md-4 col-xs-8 col-sm-6 col-xl-3 `}>
                                                                <FormattedMessage
                                                                    id="Withdraw.Submit"
                                                                    defaultMessage="Submit"
                                                                />
                                                            </button>
                                                        </a>
                                                    </form>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-4 col-xl-4 col-sm-12  ps-0 side-card">
                                        <div className="card">
                                            <div className="card-header border-bottom-0 ">
                                                <div className="pie-progress-container mt-2">
                                                    <div className={`pie_progress `}>
                                                        <CircularProgressbar styles={buildStyles({


                                                            // Whether to use rounded or flat corners on the ends - can use 'butt' or 'round'
                                                            strokeLinecap: 'butt',

                                                            // Text size
                                                            textSize: '18px',


                                                            // How long animation takes to go from one percentage to another, in seconds
                                                            pathTransitionDuration: 0.5,

                                                            // Can specify path transition in more detail, or remove it entirely
                                                            // pathTransition: 'none',

                                                            // Colors
                                                            pathColor: `#4d88ff`,
                                                            textColor: '#5f5f5f',
                                                            trailColor: '#d6d6d6',
                                                            backgroundColor: '#4d88ff',
                                                        })}
                                                                             value={percentage}
                                                                             text={`${percentage}%`}/>
                                                    </div>
                                                    <div className="pie_progress-title text-capitalize"
                                                         dir={context.locale == "fa" && "rtl"}>
                                                        <FormattedMessage
                                                            id="Withdraw.Limit"
                                                            defaultMessage="WithdrawLimit"
                                                        />
                                                    </div>
                                                    <p className="withdraw-amount text-capitalize">250/500</p>
                                                </div>
                                                <div className="Assets-container mt-4"
                                                     dir={context.locale == "fa" && "rtl"}>
                                                    {coinBalanceInfo.map((Balance) => (
                                                        <div className="row justify-content-around">
                                                            <p className="assets">
                                                                <FormattedMessage
                                                                    id="Withdraw.Assets"
                                                                    defaultMessage="Assets"
                                                                />
                                                                <br/>
                                                                <span
                                                                    className="Amount">{Math.floor((parseFloat(Balance.available) + parseFloat(Balance.frozen)) * 1000000) / 1000000} {Balance.name}</span>
                                                            </p>
                                                            <p className="frozen-assets">
                                                                <FormattedMessage
                                                                    id="Withdraw.FrozenAsset"
                                                                    defaultMessage="FrozenAsset"
                                                                />
                                                                <br/>
                                                                <span
                                                                    className="Amount">{Math.floor(Balance.frozen * 1000000) / 1000000} {Balance.name} </span>
                                                            </p>
                                                            <p className="available">
                                                                <FormattedMessage
                                                                    id="Withdraw.Available"
                                                                    defaultMessage="Available"
                                                                />
                                                                <br/>
                                                                <span
                                                                    className="Amount">{Math.floor(Balance.available * 1000000) / 1000000} {Balance.name}</span>
                                                            </p>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                            <div className="card-body list-container "
                                                 dir={context.locale == "fa" && "rtl"}>
                                                <hr/>
                                                <div className="instruction-box ">
                                                    <p className="explantation m-0">
                                                        <FormattedMessage
                                                            id="Withdraw.Attention"
                                                            defaultMessage="FrozenAsset"
                                                        />
                                                    </p>
                                                </div>
                                                <ol className="list-group list-group-numbered ">
                                                    <li className="list-group-item border-0">
                                                        <FormattedMessage
                                                            id="Withdraw.Attention.first"
                                                            defaultMessage="Attention.first"
                                                        />
                                                    </li>
                                                    <li className="list-group-item border-0">
                                                        <FormattedMessage
                                                            id="Withdraw.Attention.second"
                                                            defaultMessage="Attention.second"
                                                        />
                                                    </li>
                                                    <li className="list-group-item border-0">
                                                        <FormattedMessage
                                                            id="Withdraw.Attention.third"
                                                            defaultMessage="Attention.third"
                                                        />
                                                    </li>
                                                </ol>
                                                <hr/>
                                                <button className="btn FAQ mt-2" type="button">
                                                    <FormattedMessage
                                                        id="Withdraw.Attention.WithdrawFAQ"
                                                        defaultMessage="Attention.WithdrawFAQ"
                                                    />
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12 mt-3 ">
                                    <div className="card p-3 withdrawal-records">
                                        <div className="card-header mb-3">
                                            <h4 className="card-title" dir={context.locale == "fa" && "rtl"}>
                                                <FormattedMessage
                                                    id="Withdraw.Table.Title"
                                                    defaultMessage="Last 5 withdrawal "
                                                />
                                            </h4>
                                        </div>
                                        <table>
                                            <tbody>
                                            <tr className="withdrawal-records-title ">
                                                <th>
                                                    <FormattedMessage
                                                        id="Withdraw.Table.Time"
                                                        defaultMessage="Time"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Withdraw.Table.Coin"
                                                        defaultMessage="Coin"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Withdraw.Table.ProtocolType"
                                                        defaultMessage="ProtocolType"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Withdraw.Table.WithdrawalAddress"
                                                        defaultMessage="WithdrawalAddress"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Withdraw.Table.Status"
                                                        defaultMessage="Status"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Withdraw.Table.TXID"
                                                        defaultMessage="TXID"
                                                    />
                                                </th>
                                            </tr>
                                            <tr className="withdrawal-records-content">
                                                <td>Jill</td>
                                                <td>Smith</td>
                                                <td>50</td>
                                                <td>50</td>
                                                <td>50</td>
                                                <td>50</td>
                                            </tr>
                                            <tr className="withdrawal-records-content"></tr>
                                            <tr className="withdrawal-records-content"></tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    );
};

export default Withdraw;
