import React, {useContext, useEffect} from 'react';
import NavBar from "../widgets/NavBar";
import Footer from "../widgets/Footer";
import '../css/bootstrap.css';
import '../css/accountLevel.css';
import {CircularProgressbar, buildStyles} from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import {IoMdArrowDropdown} from "@react-icons/all-files/io/IoMdArrowDropdown";
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper";
import { useHistory } from "react-router-dom";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
import {CgListTree} from "@react-icons/all-files/cg/CgListTree";
import {MdStreetview} from "@react-icons/all-files/md/MdStreetview";
import {HiInboxIn} from "@react-icons/all-files/hi/HiInboxIn";
import {VscReferences} from "@react-icons/all-files/vsc/VscReferences";
import AccountsNavigator from "../widgets/AccountsNavigator";
import {LoginContext} from "../../Root";

const percentage = 50;


const AccountLevel = (props) => {

        let history = useHistory();

        function handleIdVerificationClick() {
            history.push("/id_verification");
        }

    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
    useEffect(() => {
        if(!isLoggedIn)
            history.push('login');
    }, []);

    const context = useContext(Context);

    return (
        <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
            <NavBar/>
            <br/>
            <br/>
            <br/>
            <br/>
            <div className="container-fluid ">
                <div className="row justify-content-center">
                    <div className="col-md-12 col-lg-12 col-xl-10  col-sm-12 col-xs-12">
                        <div className="card shadow accountLevel-container">

                      <AccountsNavigator />
                                <hr/>

                            <div className="card-header mt-3">


                                <div className="btn accts-level ">
                                    Accts
                                    <br/>
                                    <span>LV0</span>
                                </div>
                                <div className="emptyCircle rounded-circle"></div>
                                <div className="card-subtitle withdrawalLimit-container mb-3">
                                    <div className="withdrawalLimit">
                                        <h5 className="card-title text-center" dir={context.locale == "fa" && "rtl"}>
                                            <div className="btn accts-level-hidden ">
                                                <FormattedMessage
                                                    id="hiddenBtn"
                                                    defaultMessage="Account Level"
                                                />
                                            </div>
                                            <FormattedMessage
                                                id="AccountLevel.FirstCard.Withdrawal"
                                                defaultMessage="Withdrawal"
                                            />

                                            <div className="amount">10,000.00 USD</div>
                                        </h5>
                                    </div>
                                </div>
                                <hr/>
                            </div>
                            <div className="card-body">
                                <div className="verification-steps mt-4 ">
                                    <ul className="ACC-tilesWrap ">
                                        <li className={`Acc-first-card`} >
                                            <h2>1</h2>
                                            <h3>
                                                <div className="basicInfo-container mt-1">
                                                    <div className="basicInfo-text ">
                      <span className="card-text" dir={context.locale == "fa" && "rtl"}>
                             <FormattedMessage
                                 id="AccountLevel.StepsCards.CompleteMobile"
                                 defaultMessage="CompleteMobile"
                             />
                          <br/>
                         <FormattedMessage
                             id="AccountLevel.StepsCards.CompleteMobile.sec"
                             defaultMessage="TOTP binding"
                         />
                      </span>
                                                        <div className="center2">
                                                            <p>
                                                                <input
                                                                    type="checkbox"
                                                                    name="A"
                                                                    className="Checkbox box"
                                                                    defaultChecked
                                                                    disabled
                                                                />
                                                            </p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </h3>
                                        </li>
                                        <li className={` ${!props.isVerified ? "not-verifiedLI" : "Acc-second-card"}`} onClick={handleIdVerificationClick}>
                                            <h2 className={`${!props.isVerified ? "not-verified" : ""}`}>2</h2>
                                            <h3 className={`${!props.isVerified ? "not-verified" : ""}`}>
                                                <div className="basicInfo-container mt-1">
                    <span className="basicInfo-text" dir={context.locale == "fa" && "rtl"}>
                            <FormattedMessage
                                id="AccountLevel.StepsCards.ID"
                                defaultMessage="ID"
                            />
                        <br/>
                               <FormattedMessage
                                   id="AccountLevel.StepsCards.ID.sec"
                                   defaultMessage="Verification"
                               />{" "}
                    </span>{" "}
                                                </div>
                                                <div className="center2">
                                                    <p>
                                                        <input type="checkbox" name="A" className="Checkbox box"
                                                               disabled/>
                                                    </p>
                                                </div>
                                            </h3>
                                        </li>
                                        <li className={` ${!props.isVerified ? "not-verifiedLI" : "Acc-third-card"}`}>
                                            <h2>3</h2>
                                            <h3 className={`${!props.isVerified ? "not-verified" : ""}`}>
                                                <div className="basicInfo-container mt-1">
                    <span className="basicInfo-text" dir={context.locale == "fa" && "rtl"}>
                       <FormattedMessage
                           id="AccountLevel.StepsCards.Contact"
                           defaultMessage="Contact"
                       />
                        <br/>
                     <FormattedMessage
                         id="AccountLevel.StepsCards.Contact.sec"
                         defaultMessage="Support"
                     />
                    </span>
                                                </div>
                                                <div className="center2">
                                                    <p>
                                                        <input type="checkbox" name="A" className="Checkbox box"
                                                               disabled/>
                                                    </p>
                                                </div>
                                            </h3>
                                        </li>
                                    </ul>
                                </div>
                            </div>

                                <hr/>

                            <div className="card-header">

                                    <div className="btn vip-level">
                                        VIP
                                        <br/>
                                        LV0
                                    </div>
                                    <div className="emptyCircle rounded-circle"></div>
                                    <div className="card-subtitle VipLevel-container ">
                                        <div className=" VipLevel">
                                            <h5 className="card-title text-center" dir={context.locale == "fa" && "rtl"}>
                                                <div className="btn vip-level-hidden">
                                                    VIP
                                                    <span>Level</span>
                                                </div>
                                                <FormattedMessage
                                                    id="AccountLevel.Second"
                                                    defaultMessage="AccountLevel.Second"
                                                /> <br/>
                                                <FormattedMessage
                                                    id="AccountLevel.ProgressBar.CurrentCETHolding.sec"
                                                    defaultMessage=""
                                                />
                                            </h5>
                                        </div>
                                    </div>
                                </div>


                                <div className="card-body">


                                    <div className="row">
                                        <div
                                            className="withdrawTable-container col-lg-6 col-md-12 col-sm-12 col-xs-12 mt-4"
                                            style={{overflowX: "auto"}}>

                                            <div className="pie-progress-container ">

                                                <div className={`pie_progress `}>
                                                    <CircularProgressbar styles={buildStyles({


                                                        // Whether to use rounded or flat corners on the ends - can use 'butt' or 'round'
                                                        strokeLinecap: 'butt',

                                                        // Text size
                                                        textSize: '18px',


                                                        // How long animation takes to go from one percentage to another, in seconds
                                                        pathTransitionDuration: 0.5,

                                                        // Can specify path transition in more detail, or remove it entirely
                                                        // pathTransition: 'none',

                                                        // Colors
                                                        pathColor: `#4d88ff`,
                                                        trailColor: '#d6d6d6',
                                                        textColor: '#5f5f5f',
                                                        backgroundColor: '#4d88ff',
                                                    })}
                                                                         value={percentage} text={`${percentage}%`}/>
                                                </div>

                                                <div className="pie_progress-title text-capitalize" dir={context.locale == "fa" && "rtl"}>
                                                    <FormattedMessage
                                                        id="AccountLevel.ProgressBar.CurrentCETHolding"
                                                        defaultMessage="CurrentCETHolding"
                                                    />
                                                    <div className="amount" dir={"ltr"}>
                                                        <FormattedMessage
                                                            id="AccountLevel.ProgressBar.Amount"
                                                            defaultMessage="Amount"
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="VIP_level" dir={context.locale == "fa" && "rtl"}>
                                                <h2 className="card-title p-3">VIP Level</h2>
                                            </div>
                                            <table>
                                                <tbody>
                                                <tr className="title-container">
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.table.VIPLevel"
                                                            defaultMessage="VIPLevel"
                                                        />
                                                    </th>
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.table.CETHolding"
                                                            defaultMessage="CETHolding"
                                                        />
                                                    </th>
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.table.SpotFees"
                                                            defaultMessage="SpotFees"
                                                        />
                                                    </th>
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.table.MarginDaily"
                                                            defaultMessage="MarginDaily"
                                                        />
                                                    </th>
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.table.PerpetualFees"
                                                            defaultMessage="PerpetualFees"
                                                        />
                                                    </th>
                                                </tr>
                                                <tr>
                                                    <td>Jill</td>
                                                    <td>Smith</td>
                                                    <td>50</td>
                                                    <td>50</td>
                                                    <td>50</td>
                                                </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                        <div
                                            className="spot-container col-lg-6 col-md-12 col-sm-12 col-xs-12 mt-4"
                                            style={{overflowX: "auto"}}
                                        >
                                            <div className="pie-progress-container ">
                                                <div className={`pie_progress `}>
                                                    <CircularProgressbar styles={buildStyles({


                                                        // Whether to use rounded or flat corners on the ends - can use 'butt' or 'round'
                                                        strokeLinecap: 'butt',

                                                        // Text size
                                                        textSize: '18px',


                                                        // How long animation takes to go from one percentage to another, in seconds
                                                        pathTransitionDuration: 0.5,

                                                        // Can specify path transition in more detail, or remove it entirely
                                                        // pathTransition: 'none',

                                                        // Colors
                                                        pathColor: `#4d88ff`,
                                                        textColor: '#5f5f5f',
                                                        trailColor: '#d6d6d6',
                                                        backgroundColor: '#4d88ff',
                                                    })}
                                                                         value={percentage} text={`${percentage}%`}/>
                                                </div>

                                                <div className="pie_progress-title text-capitalize" dir={context.locale == "fa" && "rtl"}>
                                                    <FormattedMessage
                                                        id="AccountLevel.SecProgressBar.SpotTrading"
                                                        defaultMessage="Spot Trading "
                                                    />
                                                    <div className="amount" dir={"ltr"}>
                                                        <FormattedMessage
                                                            id="AccountLevel.SecProgressBar.Amount"
                                                            defaultMessage="Spot Trading "
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="spotMaking">
                                                <h2 className="card-title p-3" dir={context.locale == "fa" && "rtl"}>
                                                    <FormattedMessage
                                                        id="AccountLevel.ProgressBar.sec.SpotMarket"
                                                        defaultMessage="Spot Market Making"
                                                    />
                                                </h2>
                                            </div>
                                            <table>
                                                <tbody>
                                                <tr className="title-container" >
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.Table.sec.MarketMaking"
                                                            defaultMessage="MarketMaking"
                                                        />
                                                    </th>
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.Table.sec.SpotTrading"
                                                            defaultMessage="Spot Trading Volume (Last month)"
                                                        />
                                                    </th>
                                                    <th>
                                                        <FormattedMessage
                                                            id="AccountLevel.Table.sec.SpotFees"
                                                            defaultMessage="SpotFees"
                                                        />
                                                    </th>
                                                </tr>
                                                <tr>
                                                    <td>Jill</td>
                                                    <td>Smith</td>
                                                    <td>50</td>
                                                </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                    <div className="row justify-content-center mt-5 mb-4">
                                        <a className="btn spot-showMore" dir={context.locale == "fa" && "rtl"}>
                                            <FormattedMessage
                                                id="AccountLevel.ShowMore"
                                                defaultMessage="Show more"
                                            />
                                            <IoMdArrowDropdown className="ms-2 fas fa-sort-down"/>
                                        </a>
                                    </div>
                                </div>

                        </div>
                    </div>
                </div>
            </div>
            <br/>
            <br/>
            <Footer/>
        </div>
    );
};
export default AccountLevel