import React, {useCallback, useContext, useEffect, useRef, useState} from "react";
import NavBar from "../widgets/NavBar";
import '../css/bootstrap.css';
import '../css/deposit.css';
import {FaSearchDollar} from '@react-icons/all-files/fa/FaSearchDollar';
import {BiCopy} from '@react-icons/all-files/bi/BiCopy';
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper";
import {useHistory} from "react-router-dom";
import {LoginContext} from "../../Root";
import CopyToCLipBoard from 'copy-to-clipboard-react';
import MarketSearchSec from "../widgets/MarketSearchSec";
import QrCode from "../widgets/QrCode";
import axios from "axios";
import {Badge, CircularProgress} from "@material-ui/core";

export default function DepositScreen() {

    const context = useContext(Context);

    const [selectedNetworks, setNetworks] = useState();

    const [selectedOne, setSelectedOne] = useState();
    const [configs, setConfigs] = useState();
    const [chains, setChain] = useState([]);

    const [width, setWidth] = useState();

    const [address, setAddress] = useState("Copy text Button");
    const [coinType, setCoinType] = useState();
    const [copySuccess, setCopySuccess] = useState(false);


    const [IsLoading, setIsLoading] = useState(false);
    const [isLoading, setIsloading] = useState(false);
    const [error, setError] = useState(null);

    const fetchGeneralList = useCallback(async () => {
        setIsLoading(true);
        setError(null);
        try {
            const response = await fetch('/v1/common/asset/config');

            if (response.status !== 200) {
                throw new Error('Something went wrong!');
            }

            const generalLi = await response.json();
            // const data = await (response.data.data.ticker);


            const loadedData = [];


            for (var key in generalLi.data) {
                loadedData.push({
                    name: generalLi.data[key].asset,
                    deposit_least_amount: generalLi.data[key].deposit_least_amount,
                    withdraw_tx_fee: generalLi.data[key].withdraw_tx_fee,
                    chain: generalLi.data[key].chain,
                    can_deposit: generalLi.data[key].can_deposit
                });
            }

            setConfigs(loadedData);

        } catch (error) {
            setError(error.message);
        }
        setIsLoading(false);
    }, []);


    useEffect(() => {
        fetchGeneralList();
        setInterval(() => {
            setWidth(window.innerWidth);
        }, 100);
    }, [fetchGeneralList]);

    const history = useHistory();
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
    useEffect(() => {
        if (!isLoggedIn)
            history.push('login');
    }, []);

    const fetchCoinData = useCallback(async (val, networks, config) => {
        setIsLoading(true);
        setSelectedOne(null);
        setCopySuccess(false);

        const loadedConfigs = [];
        for (var key in networks) {
            if (networks[key].name === val) {
                setNetworks(networks[key]);
            }
            for (var i in config) {
                for (var j in networks[key].networks) {
                    if (networks[key].name === val && config[i].name === val && networks[key].networks[j] == config[i].chain) {
                        loadedConfigs.push({
                            name: val,
                            network: networks[key].networks[j],
                            chain: config[i].chain,
                            withdraw_tx_fee: config[i].withdraw_tx_fee
                        })

                    }
                }
            }
        }

        setChain(loadedConfigs);
        setCoinType(val);

        setIsLoading(false);
    }, []);


    let content;

    if (chains.length === 0 && selectedNetworks != undefined) {
        content = <li className="list-group-item border-0">Found no Chain.</li>;
    }

    if (error) {
        content = <li className="list-group-item border-0">{error}</li>;
    }

    if (IsLoading) {
        content = <li className="list-group-item border-0"><CircularProgress/></li>;
    }

    const fetchAddress = useCallback(async (val, loadedConfigs) => {
        setIsloading(true);
        let smart_contract_name;
        if (loadedConfigs.chain == undefined)
            smart_contract_name = loadedConfigs[0].chain;
        smart_contract_name = loadedConfigs.chain;
        const Addresses = await axios.get(`/core/balanceDepositAddress?coin_type=${val}&smart_contract_name=${smart_contract_name}` , {
            headers:{
                'Authorization': "Bearer " + localStorage.getItem("token")
            }
        });
        if (Addresses.status !== 200) {
            throw new Error('Something went wrong!');
        }
        if (Addresses.data.data.coin_address) {
            setAddress(Addresses.data.data.coin_address);
            setCopySuccess(true);
        } else {
            setCopySuccess(false);
        }

        setIsloading(false);

    }, [])



    let adressContent;


    if (chains.length === 0 && selectedNetworks != undefined) {
        adressContent = <li className="list-group-item border-0">Found no Chain.</li>;
    }

    if (isLoading) {
        adressContent = <li className="list-group-item border-0"><CircularProgress/></li>;
    }

    return (
        <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
            <NavBar/>
            <br/>
            <br/>
            <br/>
            <br/>
            <div className="container-fluid ">
                <div className="row justify-content-center mb-3">
                    <div className="col-md-10 col-lg-10 col-xl-8 col-xxl-8 col-sm-12 col-xs-12 ">
                        <div className="card">
                            <div className="card-header">
                                <div className="Deposit-title-container ms-1" dir={context.locale == "fa" && "rtl"}>
                                    <p className="title m-0">
                                        <FormattedMessage
                                            id="Deposit.Title"
                                            defaultMessage="Deposit"
                                        />
                                    </p>
                                    <span className="explain">
                 <FormattedMessage
                     id="Deposit.SubTitle"
                     defaultMessage="SubTitle"
                 />
            </span>
                                </div>
                            </div>
                            <div className="card-body">
                                <div className="row">
                                    <div className="col-lg-8 col-xl-8 col-sm-12 col-xs-12">
                                        <div className="card deposit-card">
                                            <div className="card-header border-0" dir={context.locale == "fa" && "rtl"}>
                                                <label className="fw-bold">Coin</label>
                                                <MarketSearchSec
                                                    configData={configs}
                                                    onFetchCoinData={fetchCoinData}
                                                    size={width < 500 ? 200 : 300}
                                                    lable={<FormattedMessage
                                                        id="Deposit.Coin"
                                                        defaultMessage="Coin"/>}
                                                />
                                            </div>
                                            <div className="card-body">
                                                <div className="Protocols">
                                                    <h5 className="Protocol-Type mt-3 mb-4"
                                                        dir={context.locale == "fa" && "rtl"}>
                                                        <FormattedMessage
                                                            id="Deposit.ProtocolType"
                                                            defaultMessage="ProtocolType"
                                                        />
                                                    </h5>
                                                    <ul className="list-group list-group-horizontal-md Protocol-list ">
                                                        {chains.map((chains, index) => (
                                                            <li className="list-group-item col-3">
                                                                <a className="col-12" href="#">
                                                                    <button onClick={() => {
                                                                        setSelectedOne(index);
                                                                        fetchAddress(coinType, chains);
                                                                    }}
                                                                            className={`${selectedOne == index ? 'protocolBtn-selected' : 'protocolBtn'}`}>
                                                                        {
                                                                            chains.network

                                                                        }
                                                                        <br/>
                                                                        <span className="fee-container">
                                                          <div className="fee-amount"></div>
                                                                            {" "}
                                                        </span>
                                                                    </button>
                                                                </a>
                                                            </li>
                                                        ))}
                                                    </ul>
                                                    <section
                                                        className='card-text text-center protocol-feedback'>{content}</section>
                                                </div>
                                                <div className={`Completion`}>

                                                    <h5 className="address mt-4 mb-5 d-lg-flex justify-content-between"
                                                        dir={context.locale == "fa" && "rtl"}>
                                                        <FormattedMessage
                                                            id="Deposit.DepositTo"
                                                            defaultMessage="DepositTo"
                                                        />
                                                    </h5>
                                                    <section
                                                        className='card-text text-center protocol-feedback'>{adressContent}</section>
                                                    {copySuccess && !isLoading &&
                                                    <div className="row justify-content-center m-5 m-sm-0">
                                                        <div
                                                            className="QR-code d-flex justify-content-center">
                                                            <QrCode size={width < 400 ? 120 : 200} value={address}/>
                                                        </div>

                                                        <div className=" row justify-content-center mt-3">
                                                            <div
                                                                className="CopyToCLipBoard d-flex justify-content-center">
                                                                <Badge  badgeContent={<BiCopy/>} color="secondary" >
                                                                    <CopyToCLipBoard
                                                                        text={address}
                                                                    />
                                                                </Badge>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-4 col-xl-4 col-sm-12 col-xs-12">
                                        <div className="card border-0" dir={context.locale == "fa" && "rtl"}>
                                            <div className="card-header">
                                                <div className="instruction-box ">
                                                    <p className="explantation m-0">
                                                        <FormattedMessage
                                                            id="Deposit.Attention"
                                                            defaultMessage="Attention"
                                                        />
                                                    </p>
                                                    <p className="text-center title mt-2">
                                                        <FormattedMessage
                                                            id="Deposit.Attention.first"
                                                            defaultMessage="Attention"
                                                        />
                                                    </p>
                                                </div>
                                            </div>
                                            <div className="card-body list-container">
                                                <ol className="list-group list-group-numbered ">
                                                    <li className="list-group-item border-0">
                                                        <FormattedMessage
                                                            id="Deposit.Attention.second"
                                                            defaultMessage="Attention"
                                                        />
                                                    </li>
                                                    <li className="list-group-item border-0">
                                                        <FormattedMessage
                                                            id="Deposit.Attention.third"
                                                            defaultMessage="Attention"
                                                        />
                                                    </li>
                                                    <li className="list-group-item border-0">
                                                        <FormattedMessage
                                                            id="Deposit.Attention.forth"
                                                            defaultMessage="Attention"
                                                        />
                                                    </li>
                                                </ol>
                                                <hr/>
                                                <button className="btn FAQ mt-2" type="button">
                                                    <FormattedMessage
                                                        id="Deposit.DepositFAQ"
                                                        defaultMessage="DepositFAQ"
                                                    />
                                                </button>
                                                {/*<h5 className="go-trade mt-3 mb-4">*/}
                                                {/*    <FormattedMessage*/}
                                                {/*        id="Deposit.GoTrade"*/}
                                                {/*        defaultMessage="GoTrade"*/}
                                                {/*    />*/}
                                                {/*</h5>*/}
                                                {/*<div className="d-grid gap-2 d-md-block d-flex mt-3">*/}
                                                {/*    <button className="go-trade-usdt" type="button">*/}
                                                {/*        <FormattedMessage*/}
                                                {/*            id="Deposit.BTC/USDT"*/}
                                                {/*            defaultMessage="Deposit.BTC/USDT"*/}
                                                {/*        />*/}
                                                {/*    </button>*/}
                                                {/*</div>*/}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12 mt-3 ">
                                    <div className="card p-3 deposit-records">
                                        <div className="card-header mb-3">
                                            <h4 className="card-title" dir={context.locale == "fa" && "rtl"}>
                                                <FormattedMessage
                                                    id="Deposit.Table.Title"
                                                    defaultMessage="Last 5 Deposit records"
                                                />
                                            </h4>
                                        </div>
                                        <table>
                                            <tbody>
                                            <tr className="deposit-records-title ">
                                                <th>
                                                    <FormattedMessage
                                                        id="Deposit.Table.Time"
                                                        defaultMessage="Time"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Deposit.Table.Coin"
                                                        defaultMessage="Coin"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Deposit.Table.ProtocolType"
                                                        defaultMessage="ProtocolType"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Deposit.Table.DepositAddress"
                                                        defaultMessage="DepositAddress"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Deposit.Table.Status"
                                                        defaultMessage="Status"
                                                    />
                                                </th>
                                                <th>
                                                    <FormattedMessage
                                                        id="Deposit.Table.TXID"
                                                        defaultMessage="TXID"
                                                    />
                                                </th>
                                            </tr>
                                            <tr className="deposit-records-content">
                                                <td>Jill</td>
                                                <td>Smith</td>
                                                <td>50</td>
                                                <td>50</td>
                                                <td>50</td>
                                                <td>50</td>
                                            </tr>
                                            <tr className="deposit-records-content"></tr>
                                            <tr className="deposit-records-content"></tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <br/>
        </div>
    );
}
