import React, {useContext, useEffect, useState, useCallback} from 'react';
import '../css/bootstrap.css';
import '../css/MarketList.css';
import NavBar from "../widgets/NavBar";
import Footer from "../widgets/Footer";
import {FaSearchDollar} from "@react-icons/all-files/fa/FaSearchDollar";
import {FaHashtag} from "@react-icons/all-files/fa/FaHashtag";
import {AiFillCaretUp} from "@react-icons/all-files/ai/AiFillCaretUp";
import {AiFillCaretDown} from "@react-icons/all-files/ai/AiFillCaretDown";
import {FaAngleDoubleRight} from "@react-icons/all-files/fa/FaAngleDoubleRight";
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper";

import axios from "axios";
import {CircularProgress} from "@material-ui/core";
import {useHistory} from "react-router-dom";


const MarketList = () => {

    const context = useContext(Context);
    const history = useHistory();

    const [tickers, setTickers] = useState([]);

    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);


    const fetchCoinHandler = useCallback(async () => {
        setIsLoading(true);
        setError(null);
        try {

            const listResponse = await axios.get('currencies/list');

            if (listResponse.status !== 200) {
                throw new Error('Something went wrong with CoinList!');
            }

            const marketL = (listResponse.data);

            const loadedList = [];


            for (var keys in marketL) {
                loadedList.push({
                    name: marketL[keys].markets,
                    rank: marketL[keys].rank
                });
            }

            loadedList.sort(function (a,b){return a.rank - b.rank});


            const response = await axios.get('core/marketTickerAll');

            if (response.status !== 200) {
                throw new Error('Something went wrong!');
            }

            const data = await (response.data.data.ticker);

            const loadedData = [];

            for (var i = 0; i <= loadedList.length; i++) {
                for (var key in data) {
                    if (loadedList[i] != undefined && loadedList[i].name != undefined && key.toLowerCase() == loadedList[i].name[0].toLowerCase()) {
                        loadedData.push({
                            id: i + 1,
                            name: key,
                            lastPrice: data[key].last,
                            dayOpen: data[key].open,
                            marketCap: data[key].buy_amount
                        });
                    }
                }
            }


            setTickers(loadedData);

        } catch (error) {
            console.log(error);
            setError(error.message);
        }
        setIsLoading(false);
    }, []);

    useEffect(() => {
        fetchCoinHandler();
    }, [fetchCoinHandler]);

    let content;

    if (tickers.length === 0) {
        content = <p>Found no Coin.</p>;
    }

    if (error) {
        content = <p>{error}</p>;
    }

    if (isLoading) {
        content = <p><CircularProgress/></p>;
    }

    //-------------------------------------------------------------------------------------------------------------------------------------------------
    const [currentSearch, setCurrentSearch] = React.useState("");
    const [nameSortActiveINC, setNameSortActiveINC] = React.useState(false);
    const [nameSortActiveDEC, setNameSortActiveDEC] = React.useState(false);
    const [priceSortActiveINC, setPriceSortActiveINC] = React.useState(false);
    const [priceSortActiveDEC, setPriceSortActiveDEC] = React.useState(false);
    const [percentSortActiveDEC, setPercentSortActiveDEC] = React.useState(false);
    const [percentSortActiveINC, setPercentSortActiveINC] = React.useState(false);
    const [marketSortActiveDEC, setMarketSortActiveDEC] = React.useState(false);
    const [marketSortActiveINC, setMarketSortActiveINC] = React.useState(false);


    function compareNameINC(object1, object2, key) {
        const obj1 = object1[key].toUpperCase()
        const obj2 = object2[key].toUpperCase()

        if (obj1 < obj2) {
            return -1
        }
        if (obj1 > obj2) {
            return 1
        }
        return 0
    }

    function compareNameDEC(object1, object2, key) {
        const obj1 = object1[key].toUpperCase()
        const obj2 = object2[key].toUpperCase()

        if (obj1 > obj2) {
            return -1
        }
        if (obj1 < obj2) {
            return 1
        }
        return 0
    }

    function compareNumberINC(object1, object2, key) {
        const obj1 = object1[key]
        const obj2 = object2[key]

        return obj2 - obj1
    }

    function compareNumberDEC(object1, object2, key) {
        const obj1 = object1[key]
        const obj2 = object2[key]

        return obj1 - obj2
    }

    function comparePercentINC(object1, object2, key1, key2) {
        const obj1 = object1[key1] / object1[key2]
        const obj2 = object2[key1] / object2[key2]

        return obj2 - obj1
    }

    function comparePercentDEC(object1, object2, key1, key2) {
        const obj1 = object1[key1] / object1[key2]
        const obj2 = object2[key1] / object2[key2]

        return obj1 - obj2
    }


    const sortListNameINC = (tickers) => {
        setNameSortActiveDEC(false);
        setPriceSortActiveINC(false);
        setPriceSortActiveDEC(false);
        setPercentSortActiveDEC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveDEC(false);
        setMarketSortActiveINC(false)

        if (!nameSortActiveINC) {
            tickers.sort((ticker1, ticker2) => {
                return compareNameINC(ticker1, ticker2, 'name')
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'id')
            })
        }
    }

    const sortListNameDEC = (tickers) => {
        setNameSortActiveINC(false);
        setPriceSortActiveINC(false);
        setPriceSortActiveDEC(false);
        setPercentSortActiveDEC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveDEC(false);
        setMarketSortActiveINC(false)

        if (!nameSortActiveDEC) {
            tickers.sort((ticker1, ticker2) => {
                return compareNameDEC(ticker1, ticker2, 'name')
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'id')
            })
        }
    }

    const sortListPriceINC = (tickers) => {
        setPriceSortActiveDEC(false);
        setNameSortActiveDEC(false);
        setNameSortActiveINC(false);
        setPercentSortActiveDEC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveDEC(false);
        setMarketSortActiveINC(false)

        if (!priceSortActiveINC) {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberINC(ticker1, ticker2, 'lastPrice')
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'id')
            })
        }
    }

    const sortListPriceDEC = (tickers) => {
        setPriceSortActiveINC(false);
        setNameSortActiveDEC(false);
        setNameSortActiveINC(false);
        setPercentSortActiveDEC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveDEC(false);
        setMarketSortActiveINC(false)

        if (!priceSortActiveDEC) {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'lastPrice')
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberINC(ticker1, ticker2, 'id')
            })
        }
    }

    const sortListPercentINC = (tickers) => {
        setPriceSortActiveINC(false)
        setPriceSortActiveDEC(false);
        setNameSortActiveDEC(false);
        setNameSortActiveINC(false);
        setPercentSortActiveDEC(false);
        setMarketSortActiveDEC(false);
        setMarketSortActiveINC(false);

        if (!percentSortActiveINC) {
            tickers.sort((ticker1, ticker2) => {
                return (comparePercentINC(ticker1, ticker2, 'lastPrice', 'dayOpen'))
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'id')
            })
        }
    }

    const sortListPercentDEC = (tickers) => {
        setPriceSortActiveINC(false)
        setPriceSortActiveDEC(false);
        setNameSortActiveDEC(false);
        setNameSortActiveINC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveDEC(false);
        setMarketSortActiveINC(false);

        if (!percentSortActiveDEC) {
            tickers.sort((ticker1, ticker2) => {
                return (comparePercentDEC(ticker1, ticker2, 'lastPrice', 'dayOpen'))
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'id')
            })
        }
    }

    const sortListMarketCapINC = (tickers) => {
        setPriceSortActiveINC(false)
        setPriceSortActiveDEC(false);
        setNameSortActiveDEC(false);
        setNameSortActiveINC(false);
        setPercentSortActiveDEC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveDEC(false);

        if (!marketSortActiveINC) {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberINC(ticker1, ticker2, 'marketCap')
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'id')
            })
        }
    }

    const sortListMarketCapDEC = (tickers) => {
        setPriceSortActiveINC(false)
        setPriceSortActiveDEC(false);
        setNameSortActiveDEC(false);
        setNameSortActiveINC(false);
        setPercentSortActiveDEC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveINC(false);

        if (!marketSortActiveDEC) {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'marketCap')
            })
        } else {
            tickers.sort((ticker1, ticker2) => {
                return compareNumberDEC(ticker1, ticker2, 'id')
            })
        }
    }

    const defaultSort = (tickers) => {
        setPriceSortActiveINC(false)
        setPriceSortActiveDEC(false);
        setNameSortActiveDEC(false);
        setNameSortActiveINC(false);
        setPercentSortActiveDEC(false);
        setPercentSortActiveINC(false);
        setMarketSortActiveINC(false);
        setMarketSortActiveDEC(false);

        tickers.sort((ticker1, ticker2) => {
            return compareNumberDEC(ticker1, ticker2, 'id')
        })
    }
    //******************************************************************************************************************************************

    console.log(tickers)

    return (
        <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
            <NavBar/>
            <br/>
            <br/>
            <br/>
            <div className="container-fluid  ">
                <div className="row justify-content-center">
                    <div className="col-xs-8 mt-3 col-md-8 col-sm-12 col-xs-12">
                        <div className="card  text-center border-0">
                            <div className="card-header d-flex justify-content-between marketList-header">
                                <ul className="nav nav-tabs card-header-tabs">
                                    <li className="nav-item">
                                        <h4 className="nav-link active mt-3" aria-current="true" href="#">
                                            <FormattedMessage
                                                id="MarketList.Title"
                                                defaultMessage="Market List"
                                            />
                                        </h4>
                                    </li>
                                </ul>
                                <div className="input-group marketList-crypto-search ">
                                    <input
                                        type="text"
                                        className="form-control "
                                        placeholder="..."
                                        aria-label="Recipient's username"
                                        aria-describedby="button-addon2"
                                        onChange={e => setCurrentSearch(e.target.value)}
                                    />
                                    <button className="btn " type="button" id="button-addon2">
              <span>
                <FaSearchDollar className="fas fa-search-dollar"/>
              </span>
                                    </button>
                                </div>
                            </div>
                            <div className="card-body shadow marketList-body">
                                <div
                                    className="tab-pane  crypto-table"
                                    id="market-table"
                                    role="tabpanel"
                                    aria-labelledby="pills-home-tab"
                                >

                                    <table className="market-table bg-transparent text-center ">
                                        <thead>
                                        <tr className="crypto-property">
                                            <th className={`fa-sort ${!nameSortActiveINC && !nameSortActiveDEC && !priceSortActiveINC && !priceSortActiveDEC && !percentSortActiveINC && !percentSortActiveDEC && !marketSortActiveINC && !marketSortActiveDEC && 'sorted text'}`}
                                                scope="col" onClick={() => {
                                                defaultSort(tickers)
                                            }}><FaHashtag/></th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="MarketList.Name"
                                                    defaultMessage="Name"
                                                />{" "}
                                                <span>
                      <AiFillCaretUp onClick={() => {
                          setNameSortActiveINC(!nameSortActiveINC);
                          sortListNameINC(tickers)
                      }} className={`fas fa-sort ${nameSortActiveINC && 'sorted'}`}/>
                      <AiFillCaretDown onClick={() => {
                          setNameSortActiveDEC(!nameSortActiveDEC);
                          sortListNameDEC(tickers)
                      }} className={`fas fa-sort ${nameSortActiveDEC && 'sorted'}`}/>

                    </span>
                                            </th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="MarketList.LatestPrice"
                                                    defaultMessage="LatestPrice"
                                                />(USD){" "}
                                                <span>
                        <AiFillCaretUp onClick={() => {
                            setPriceSortActiveINC(!priceSortActiveINC);
                            sortListPriceINC(tickers)
                        }} className={`fas fa-sort ${priceSortActiveINC && 'sorted'}`}/>
                      <AiFillCaretDown onClick={() => {
                          setPriceSortActiveDEC(!priceSortActiveDEC);
                          sortListPriceDEC(tickers)
                      }} className={`fas fa-sort ${priceSortActiveDEC && 'sorted'}`}/>
                    </span>
                                            </th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="MarketList.24HChange"
                                                    defaultMessage="24HChange"
                                                />{" "}
                                                <span>
                        <AiFillCaretUp onClick={() => {
                            setPercentSortActiveINC(!percentSortActiveINC);
                            sortListPercentINC(tickers)
                        }} className={`fas fa-sort ${percentSortActiveINC && 'sorted'}`}/>
                      <AiFillCaretDown onClick={() => {
                          setPercentSortActiveDEC(!percentSortActiveDEC);
                          sortListPercentDEC(tickers)
                      }} className={`fas fa-sort ${percentSortActiveDEC && 'sorted'}`}/>
                    </span>
                                            </th>
                                            <th scope="col">
                                                <FormattedMessage
                                                    id="MarketList.MarketCap"
                                                    defaultMessage="MarketCap"
                                                />
                                                <span>
                        <AiFillCaretUp onClick={() => {
                            setMarketSortActiveINC(!marketSortActiveINC);
                            sortListMarketCapINC(tickers)
                        }} className={`fas fa-sort ${marketSortActiveINC && 'sorted'}`}/>
                      <AiFillCaretDown onClick={() => {
                          setMarketSortActiveDEC(!marketSortActiveDEC);
                          sortListMarketCapDEC(tickers)
                      }} className={`fas fa-sort ${marketSortActiveDEC && 'sorted'}`}/>

                    </span>
                                            </th>
                                            <th scope="col"/>
                                        </tr>
                                        </thead>

                                        <tbody id='lists'>
                                        {tickers.filter(ticker => {
                                            if (currentSearch === "") {
                                                return ticker
                                            } else if (ticker.name.toLowerCase().startsWith(currentSearch.toLowerCase())) {
                                                return ticker
                                            }
                                        }).map((ticker) => (
                                            <tr className="marketListGlow" key={ticker.id}
                                                onClick={() => history.push(`Trade?pair=${ticker.name}`)}>
                                                <th scope="row">{ticker.id}</th>
                                                <td className="d-flex justify-content-center">

                                                    <p>{ticker.name}</p>{" "}
                                                </td>
                                                <td>{ticker.lastPrice}</td>
                                                <td className={`Percent ${ticker.lastPrice > ticker.dayOpen ? "inGrow" : "inLose"} `}>
                                                    {(((ticker.lastPrice - ticker.dayOpen)/ ticker.dayOpen) *100).toFixed(2)}%
                                                </td>
                                                <td>{ticker.marketCap}</td>
                                                <td>
                                                        <span aria-controls="simple-menu"
                                                              aria-haspopup="true">
                                                            <FaAngleDoubleRight
                                                                className="fas ACCIco fa-angle-double-right"/>
                                                        </span>
                                                    <span
                                                        className="table-Trade"

                                                    >
                                                        <FormattedMessage
                                                            id="Wallet.WalletTable.trade"
                                                            defaultMessage="Trade"
                                                        />
                                                    </span>

                                                </td>
                                            </tr>
                                        ))}
                                        {/*                    <tr className="first-row marketListGlow">*/}
                                        {/*                        <th scope="row">1</th>*/}
                                        {/*                        <td className="d-flex justify-content-center">*/}
                                        {/*<span>*/}
                                        {/*  <img*/}
                                        {/*      src="img/A640879E7F4AEA61E6777EA053450AF7.png"*/}
                                        {/*      className="crypto-ico"*/}
                                        {/*      alt="..."*/}
                                        {/*  />*/}
                                        {/*</span>*/}
                                        {/*                            <p>XHV</p>{" "}*/}
                                        {/*                        </td>*/}
                                        {/*                        <td>Otto</td>*/}
                                        {/*                        <td className="grow">+37.81%</td>*/}
                                        {/*                        <td>Jacob</td>*/}
                                        {/*                        <td>*/}
                                        {/*                            <FaAngleDoubleRight className="fas fa-angle-double-right"/>*/}
                                        {/*                        </td>*/}
                                        {/*                    </tr>*/}
                                        </tbody>

                                    </table>
                                    <section className='feedBack'>{content}</section>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            <br/>
            <br/>
            <Footer/>
        </div>
    );
};

export default MarketList;
