import React, {useContext, useEffect, useState} from 'react';
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
import {FormattedMessage} from "react-intl";
import {CgListTree} from "@react-icons/all-files/cg/CgListTree";
import {MdStreetview} from "@react-icons/all-files/md/MdStreetview";
import {HiInboxIn} from "@react-icons/all-files/hi/HiInboxIn";
import {VscReferences} from "@react-icons/all-files/vsc/VscReferences";
import {useHistory} from "react-router-dom";
import {Theme} from "../ThemeContext";

const AccountsNavigator = () => {

    const history = useHistory();
    const theme = useContext(Theme);

    const styling = theme.theme == 'dark' ? { color:"#fff"} : {color:"#333"};

    return (
        <div>
            <BottomNavigation
                style={{backgroundColor: 'transparent', margin: '15px 0 0 0'}}
                className="mb-3 "

                showLabels
            >
                <BottomNavigationAction style={styling} onClick={() => history.push("account")}
                                        className="ACC-nav-btns"
                                        label={<FormattedMessage id="Account.Info.AccountSettings"
                                                                 defaultMessage="AccountSettings"/>}
                                        icon={<CgListTree/>}/>
                <BottomNavigationAction style={styling} onClick={() => history.push("accountLevel")} className="ACC-nav-btns"
                                        label={<FormattedMessage id="Account.Info.AccountLevel"
                                                                 defaultMessage="AccountLevel"/>}
                                        icon={<MdStreetview/>}/>
                <BottomNavigationAction style={styling} onClick={() => history.push("messages")} className="ACC-nav-btns"
                                        label={<FormattedMessage id="Account.Info.MessageCenter"
                                                                 defaultMessage="MessageCenter"/>} icon={<HiInboxIn/>}/>
                <BottomNavigationAction style={styling} onClick={() => history.push("settings")} className="ACC-nav-btns"
                                        label={<FormattedMessage id="Account.Info.Settings" defaultMessage="Setting"/>}
                                        icon={<VscReferences/>}/>
            </BottomNavigation>
        </div>
    );
};

export default AccountsNavigator;
