import * as React from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete, {createFilterOptions} from '@mui/material/Autocomplete';
import parse from 'autosuggest-highlight/parse';
import match from 'autosuggest-highlight/match';
import {useHistory} from "react-router-dom";
import {useCallback, useContext, useEffect, useState} from "react";
import axios from "axios";
import {Theme} from "../ThemeContext";



const MarketSearchSec = (props) => {


    const [marketList, setMarketList] = useState([]);
    const [networks, setNetworks] = useState([]);
    const [fullNameMarket, setFullNameMarket] = useState([]);

    const [isLoading, setIsLoading] = useState(false);
    const [isMarket, setIsMarket] = useState(false);
    const [error, setError] = useState(null);

    const fetchCoinHandler = useCallback(async () => {
        setIsLoading(true);
        setError(null);
        try {

            const listResponse = await axios.get('currencies/list');

            if (listResponse.status !== 200) {
                throw new Error('Something went wrong with CoinList!');
            }


            const marketL = (listResponse.data);


            const loadedCoinList = [];
            const loadedSecCoinList = [];
            const loadedNetwork = [];

            for (var keys in marketL) {
                if (marketL[keys].short_name != null) {
                    loadedCoinList.push(
                        marketL[keys].short_name
                    );
                    loadedNetwork.push({
                            id: keys,
                            name: marketL[keys].short_name,
                            networks: marketL[keys].networks
                        }
                    );
                }
            }

            setMarketList(loadedCoinList);
            setNetworks(loadedNetwork);

        } catch (error) {
            setError(error.message);
        }
        setIsLoading(false);
    }, []);

    const [val, setVal] = useState();
    const theme = useContext(Theme);
    const styling = theme.theme == 'dark' ? { paddingTop:5 , backgroundColor:"#cecece" , borderRadius:4 } : { borderRadius:4 , paddingTop:5 };

    useEffect(() => {
        fetchCoinHandler();
        if (props.onFetchCoinData) {
            props.onFetchCoinData(val, networks, props.configData);
        }
        if (props.onSocket && val != undefined && props.onGetMarketDetail) {
            props.onSocket(val + "USDT");
            props.onGetMarketDetail(val);
            props.setFirst(val);
        }
        if (props.market == true && val != undefined) {
            setIsMarket(true);
            props.setSecond(val);
        }
    }, [fetchCoinHandler, val]);


    const filterOptions = createFilterOptions({
        matchFrom: 'start'
    });


    return (
        <Autocomplete
            id="highlights-demo"
            sx={{width: props.size , padding:0}}
            options={marketList}
            filterSelectedOptions={true}
            onChange={
                (event, value) => {
                    setVal(value);
                }
            }
            value={val}
            filterOptions={filterOptions}
            autoHighlight={true}
            clearIcon={false}
            renderInput={(params) => (
                <TextField autoFocus={true} {...params}
                            variant="outlined" style={styling} margin="normal"/>
            )}
        />
    );
};

export default MarketSearchSec;
