import React, {useCallback, useContext, useEffect, useRef, useState} from 'react';
import NavBar from "../widgets/NavBar";
import "../css/forgetPawword.css";
import axios from "axios";
import {CountdownCircleTimer} from "react-countdown-circle-timer";
import {useHistory} from "react-router-dom";
import {LoginContext} from "../../Root";
import {useToasts} from "react-toast-notifications";
import {CircularProgress} from "@material-ui/core";

const ForgetPassword = () => {

    const history = useHistory();
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);

    const [isLoading, setIsLoading] = useState(false);
    const [Timer, setTimer] = useState(false);

    const emailRef = useRef("");
    const codeRef = useRef("");
    const passRef = useRef("");
    const repPassRef = useRef("");
    const userName = useRef("");
    const tempToken = useRef("");

    const [enteredEmail, setEnteredEmail] = useState("");
    const [pass, setPass] = useState("");
    const [repPass, setRepPass] = useState("");
    const [enteredCode, setEnteredCode] = useState("");
    const [nextStep, setNextStep] = useState(false);

    const {addToast} = useToasts();

    const getCode = useCallback(async (event) => {
        setIsLoading(true);
        event.preventDefault();
        const email = emailRef.current.value;
        localStorage.setItem( "username" , email);
        try {
            const emailCode = await axios.post('auth/2fa/get_code', {"username": email}, {
                headers: {"Content-Type": "application/x-www-form-urlencoded"},
            });

            if (emailCode.status !== 200) {
                throw new Error('Something went wrong with Code!');
            }

            const Code = (emailCode.data.data);

            localStorage.setItem( "tempToken" , Code.tmp_token);


            setNextStep(true);
            addToast("Check Your email !", {appearance: "info", autoDismiss: true});

        } catch (error) {
            console.log(error);
            addToast(error.toString(), {appearance: "error", autoDismiss: true});
        }
        setIsLoading(false);
    }, []);


    const resetPassword = useCallback(async (event) => {
        setIsLoading(true);
        event.preventDefault();

        const code = codeRef.current.value;
        const pass = passRef.current.value;
        const RepPass = repPassRef.current.value;
        const tempToken = localStorage.getItem( "tempToken");
        const userName = localStorage.getItem( "username");

        const resetPassRequest = {
            "tmp_token": tempToken,
            "email_code": code,
            "username": userName,
            "new_passwords": {
                "new_password": pass,
                "new_password_confirm": RepPass
            }
        }

        console.log(resetPassRequest);

        try {
            const RestCode = await axios.post('auth/forget/reset_password', resetPassRequest, {
                headers: {"Content-Type": "application/x-www-form-urlencoded"},
            });

            if (RestCode.status !== 200) {
                throw new Error('Something went wrong with Code!');
            }

            const Code = (RestCode.data.data);
            console.log(RestCode);

            setNextStep(false);

            addToast("Your Password was change successfully", {appearance: "success", autoDismiss: true});

        } catch (error) {
            console.log(error);
            addToast(error.toString(), {appearance: "error", autoDismiss: true});
        }

        setIsLoading(false);
    }, []);

    useEffect(() => {
        if (isLoggedIn) {
            history.push('login');
        }

    }, []);

    let content;
    if (isLoading) {
        content = <CircularProgress size={16}/>;
    }

    const renderTime = ({remainingTime}) => {
        if (remainingTime === 0) {

        }
        return (
            <div className="timer">
                <div className="value">{remainingTime}</div>
            </div>
        );
    };

    return (
        <div>
            <NavBar/>
            <br/>
            <br/>
            <br/>
            <br/>
            <div className="container-fluid">
                <div className="col-12">
                    <br/>
                    <br/>
                    <br/>
                    <div className="row justify-content-center">
                        <div className="col-xl-3 col-md-6 col-sm-10 card shadow rounded p-3">
                            <div className="card-header border-0">
                                <h3 className="card-title">Reset Password</h3>
                                <p className={`mt-4 mb-0 text-capitalize ${!nextStep && 'd-none'}`}>Please enter your
                                    email address below and we will send you
                                    information to change your password.</p>
                                <p className={`mt-4 mb-0 text-capitalize ${nextStep && 'd-none'}`}>Please enter your The
                                    code we send for You </p>
                            </div>
                            <div className="card-body border-0">
                                {!nextStep ? <form className={`form-group ${isLoading && 'disabledButton'}`} onSubmit={getCode}>
                                        <div
                                            className={`form-floating resetPass-input rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3`}>
                                            <input
                                                type="email"
                                                className="form-control border-0"
                                                id="floatingInputValue"
                                                placeholder="name@example.com"
                                                value={enteredEmail}
                                                onChange={(e) => {
                                                    setEnteredEmail(e.target.value);
                                                }}
                                                ref={emailRef}
                                            />
                                            <label>Email Address</label>
                                        </div>

                                        <button className="bnt-hover bnt" type="submit">GetCode {content}</button>

                                        {/*<CountdownCircleTimer*/}
                                        {/*    isPlaying*/}
                                        {/*    size={60}*/}
                                        {/*    duration={10}*/}
                                        {/*    colors={[["#004777", 0.33], ["#A30000"]]}*/}
                                        {/*    onComplete={() => [Timer]}*/}
                                        {/*>*/}
                                        {/*    {renderTime}*/}
                                        {/*</CountdownCircleTimer>*/}
                                    </form> :
                                    <form className={`form-group ${isLoading && 'disabledButton'}`} onSubmit={resetPassword}>
                                        <div className="row timer justify-content-end mb-4">
                                            <CountdownCircleTimer
                                                isPlaying
                                                size={45}
                                                strokeWidth={5}
                                                duration={180}
                                                colors={[["#004777", 0.50], ["#69042c", 0.75], ["#A30000"]]}
                                                onComplete={() => [Timer]}
                                            >
                                                {renderTime}
                                            </CountdownCircleTimer>
                                        </div>
                                        <div
                                            className={`form-floating resetPass-input rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3`}>
                                            <input
                                                type="text"
                                                className="form-control border-0"
                                                value={enteredCode}
                                                onChange={(e) => {
                                                    setEnteredCode(e.target.value);
                                                }}
                                                ref={codeRef}
                                            />
                                            <label >Code</label>
                                        </div>
                                        <div
                                            className={`form-floating resetPass-input rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3`}>
                                            <input
                                                type="password"
                                                className="form-control border-0"
                                                value={pass}
                                                onChange={(e) => {
                                                    setPass(e.target.value);
                                                }}
                                                ref={passRef}
                                            />
                                            <label>NewPass</label>
                                        </div>
                                        <div
                                            className={`form-floating resetPass-input rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3`}>
                                            <input
                                                type="password"
                                                className="form-control border-0"
                                                value={repPass}
                                                onChange={(e) => {
                                                    setRepPass(e.target.value);
                                                }}
                                                ref={repPassRef}
                                            />
                                            <label >NewPass Repeat</label>
                                        </div>
                                        <button className="bnt-hover bnt" type="submit">Reset
                                            Password {content}</button>

                                    </form>}


                            </div>
                        </div>

                    </div>
                </div>
            </div>

        </div>
    );
};

export default ForgetPassword;
