import React, {useContext, useEffect, useState} from "react";
import NavBar from "../widgets/NavBar";
import "../css/idVerification.css"
import {FaRegIdCard} from "@react-icons/all-files/fa/FaRegIdCard";
import {FaIdCardAlt} from "@react-icons/all-files/fa/FaIdCardAlt";
import {GrContactInfo} from "@react-icons/all-files/gr/GrContactInfo";
import {RiQrScan2Line} from "@react-icons/all-files/ri/RiQrScan2Line";
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper"
import 'react-spring-modal/styles.css';
import {CenterModal, ModalCloseTarget, ModalTitle} from "react-spring-modal";
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import {useHistory} from "react-router-dom";
import {LoginContext} from "../../Root";

export default function IdVerification(props) {

    const history = useHistory();
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
    useEffect(() => {
        if(!isLoggedIn)
            history.push('login');
    }, []);

    const context = useContext(Context);
    const [isOpen, setOpen] = useState(false);

    return (
        <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
            <NavBar/>
            <br/>
            <br/>
            <br/>
            <br/>

            <div className="container-fluid ">
                <div className="row justify-content-center">
                    <div className="col-md-10 col-lg-10 col-xl-8 col-xxl-8 col-sm-12 col-xs-12">
                        <div className="card shadow">
                            <div className="card-header id-verification-header border-0">
                                <h2 className="card-title p-4" dir={context.locale == "fa" && "rtl"}>
            <span className={`id-badge ${context.locale == "fa" ? "ms-3" : "me-3"}`}>
              <FaIdCardAlt className="fas fa-id-card-alt"/>
            </span>
                                    <FormattedMessage
                                        id="IDVerification.Title"
                                        defaultMessage="IDVerification"
                                    />
                                </h2>
                            </div>
                            <div className="card-body">
                                <div className="verification-explanation">
                                    <p className="text-capitalize card-text" dir={context.locale == "fa" && "rtl"}>
                                        <FormattedMessage
                                            id="IDVerification.Explanation"
                                            defaultMessage="Some Explanation"
                                        />
                                    </p>
                                    <ul className="list-group list-group-horizontal-md d-flex justify-content-around"
                                        dir={context.locale == "fa" && "rtl"}>
                                        <li className="list-group-item border-0">
                                            <i className="fas fa-info-circle"/>
                                            <FormattedMessage
                                                id="IDVerification.WithdrawalLimit:"
                                                defaultMessage="WithdrawalLimit:"
                                            />
                                            <span className="withdraw-amount">
                    <FormattedMessage
                        id="IDVerification.WithdrawalLimit.Amount:"
                        defaultMessage="Amount:"
                    /></span>
                                        </li>
                                        <li className="list-group-item border-0">
                                            <i className="fas fa-info-circle"/>
                                            <FormattedMessage
                                                id="IDVerification.JoinMining"
                                                defaultMessage="JoinMining"
                                            />
                                        </li>
                                    </ul>
                                    <div className="howToVerify mt-5 p-4">
                                        <h3 className="card-title howToVerify-text"
                                            dir={context.locale == "fa" && "rtl"}>
                                            <FormattedMessage
                                                id="IDVerification.VerifyTitle"
                                                defaultMessage="VerifyTitle"
                                            />
                                        </h3>
                                        <div className="row">
                                            <div className="idVerification-steps mt-2 ">
                                                <ul className=" tilesWrap">
                                                    <li className={`first-card`}>
                                                        <h2>1</h2>
                                                        <h3>
                                                            <div className="basicInfo-container">
                                                                <GrContactInfo className="fas fa-file-invoice"/>
                                                                <div className="basicInfo-text mt-1">
                                                                    <FormattedMessage
                                                                        id="IDVerification.BasicInfo"
                                                                        defaultMessage="BasicInfo"
                                                                    />
                                                                </div>
                                                            </div>
                                                        </h3>
                                                    </li>

                                                    <CenterModal isOpen={isOpen} onDismiss={() => setOpen(false)}>
                                                        <ModalTitle style={{textAlign:"center"}}>ID Verification</ModalTitle>

                                                        <div className="mt-3">
                                                            <FormControl component="fieldset">
                                                                <FormLabel component="legend" style={{color: "#9c3030"}}>ID Type</FormLabel>
                                                                <RadioGroup row aria-label="position" name="position"
                                                                            defaultValue="top">
                                                                    <FormControlLabel
                                                                        value="idCard"
                                                                        control={<Radio color="primary" style={{color: "#9c3030"}}/>}
                                                                        label="Id Card"
                                                                        labelPlacement="Id Card"
                                                                    />
                                                                    <FormControlLabel
                                                                        value="passport"
                                                                        control={<Radio color="primary" style={{color: "#9c3030"}}/>}
                                                                        label="passport"
                                                                        labelPlacement="passport"
                                                                    />
                                                                </RadioGroup>
                                                            </FormControl>
                                                        </div>

                                                        <div className="form-floating mt-3">
                                                            <select className="form-select" id="floatingSelectGrid"
                                                                    aria-label="Floating label select example">
                                                                <option selected></option>
                                                                <option value="1">One</option>
                                                                <option value="2">Two</option>
                                                                <option value="3">Three</option>
                                                            </select>
                                                            <label htmlFor="floatingSelectGrid">Nationality</label>
                                                        </div>

                                                        <div className="row mt-3 g-2">
                                                            <div className="col-md">
                                                                <div className="form-floating">
                                                                    <input
                                                                        type="text"
                                                                        className="form-control"

                                                                    />
                                                                    <label>First Name</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-md">
                                                                <div className="form-floating">
                                                                    <input
                                                                        type="email"
                                                                        className="form-control"
                                                                    />
                                                                    <label >Last Name</label>
                                                                </div>
                                                            </div>
                                                        </div>

                                                        <div className="form-floating mb-3 mt-4">
                                                            <input
                                                                type="email"
                                                                className="form-control"
                                                                id="floatingInput"
                                                                placeholder="name@example.com"
                                                            />
                                                            <label htmlFor="floatingInput">Email address</label>
                                                        </div>

                                                        <div className="mt-3 mb-4">
                                                            <input accept="image/*" id="icon-button-file" type="file"
                                                                   style={{display: "none"}}/>
                                                            <label htmlFor="icon-button-file">
                                                                <IconButton color="primary" aria-label="upload picture"
                                                                            component="span" style={{color: "#9c3030"}}>
                                                                    <PhotoCamera/>
                                                                </IconButton>
                                                                Upload The Photo
                                                            </label>
                                                        </div>

                                                        <div >

                                                            <Button variant="outlined" color="primary" style={{color: "#9c3030" , borderColor:"#9c3030"}}>
                                                                Submit
                                                            </Button>
                                                        </div>
                                                    </CenterModal>

                                                    <li className={` ${!props.isVerified ? "not-verifiedLI" : "second-card"}`}
                                                        onClick={() => setOpen(true)}>
                                                        <h2>2</h2>
                                                        <h3 className={`${!props.isVerified ? "not-verified" : ""}`}>

                        <span className="basicInfo-container mt-1">
                          <FaRegIdCard className="fas fa-id-card"/>
                          <div className="basicInfo-text">
                                <FormattedMessage
                                    id="IDVerification.UploadIDCard"
                                    defaultMessage="UploadIDCard"
                                />
                          </div>
                        </span>
                                                        </h3>
                                                    </li>
                                                    <li className={`${!props.isVerified ? "not-verifiedLI" : "third-card "}`}>
                                                        <h2>3</h2>
                                                        <h3 className={`${!props.isVerified ? "not-verified" : ""}`}>
                        <span className="basicInfo-container mt-1">
                          <RiQrScan2Line className="fas fa-portrait"/>
                          <div className="basicInfo-text">
                                 <FormattedMessage
                                     id="IDVerification.FaceRecognition"
                                     defaultMessage="FaceRecognition"
                                 />
                          </div>
                        </span>
                                                        </h3>
                                                    </li>
                                                </ul>
                                                {/*<div className={`readyVerification ${context.locale != "fa" ? "text-end" : "text-start"} p-3`} dir={context.locale == "fa" && "rtl"}>*/}
                                                {/*  <div className={`form-check ${context.locale == "fa" ? "text-end" : "text-start"}`} dir={context.locale == "fa" && "rtl"}>*/}
                                                {/*    <input className="form-check-input" type="checkbox" />*/}
                                                {/*    <label className="form-check-label">*/}
                                                {/*      <FormattedMessage*/}
                                                {/*          id="IDVerification.Promise"*/}
                                                {/*          defaultMessage="Promise"*/}
                                                {/*      />*/}
                                                {/*    </label>*/}
                                                {/*  </div>*/}
                                                {/*  <button className="bn30" disabled>*/}
                                                {/*    <FormattedMessage*/}
                                                {/*        id="IDVerification.VerificationBtn"*/}
                                                {/*        defaultMessage="VerificationBtn"*/}
                                                {/*    />*/}
                                                {/*  </button>*/}
                                                {/*</div>*/}
                                            </div>
                                            <div className="useful-info" dir={context.locale == "fa" && "rtl"}>
                                                <h3 className="card-title ">
                                                    <FormattedMessage
                                                        id="IDVerification.AttentionList"
                                                        defaultMessage="AttentionList"
                                                    />
                                                </h3>
                                                <ul>
                                                    <li>
                                                        <FormattedMessage
                                                            id="IDVerification.AttentionList.first"
                                                            defaultMessage=""
                                                        />
                                                    </li>
                                                    <li>
                                                        <FormattedMessage
                                                            id="IDVerification.AttentionList.second"
                                                            defaultMessage=""
                                                        />
                                                    </li>
                                                    <li>
                                                        <FormattedMessage
                                                            id="IDVerification.AttentionList.third"
                                                            defaultMessage=""
                                                        />
                                                    </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    );
}
