import React, {useEffect, useRef, useState, useContext} from "react";
import "../css/bootstrap.css";
import "../css/LogIn-SignIn.css";
import NavBar from "../widgets/NavBar";
import Axios from "axios";
import LoadingDialog from "../widgets/LoadingDialog";
import {MdPlayArrow} from "@react-icons/all-files/md/MdPlayArrow";
import {LoginContext, VerificationContext} from "../../Root";
import {ToastContainer, toast, Slide} from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {useHistory} from "react-router-dom";
import {FormattedMessage} from "react-intl";
import {Context} from "../../translation/Wrapper";
import useInput from "../hooks/useInput";
import {useToasts} from "react-toast-notifications";
import register from "../../serviceWorker";
import NotAllowed from "../widgets/NotAllowed";
import {FaEye} from "@react-icons/all-files/fa/FaEye";
import {FaEyeSlash} from "@react-icons/all-files/fa/FaEyeSlash";
import axios from "axios";

const LoginAndSignup = (props) => {
    const history = useHistory();
    const [email, setEmail] = useState("");
    const [pass, setPass] = useState("");
    const [remember, setRemember] = useState(false);
    const [referral, setReferral] = useState("false");

    const [isLoading, setLoading] = useState(false);

    const [showLogin, setShowLogin] = useState(!props.isSignup);

    const emailRef = useRef("");
    const passRef = useRef("");
    const referralRef = useRef("false");

    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
    const {isVerified, setIsVerified} = useContext(VerificationContext);


    const {addToast} = useToasts();

    const {
        value: enteredEmail,
        isValid: enteredEmailIsValid,
        hasError: emailInputHasError,
        valueChangeHandler: emailChangeHandler,
        inputBlurHandler: emailBlurHandler,
        reset: resetEmailInput,
    } = useInput((value) => value.includes('@'));

    const {
        value: enteredPass,
        isValid: enteredPassIsValid,
        hasError: passInputHasError,
        valueChangeHandler: passChangedHandler,
        inputBlurHandler: passBlurHandler,
        reset: resetPassInput,
    } = useInput((value) => value.trim() !== '');

    let formIsValid = false;

    if (enteredEmailIsValid && enteredPassIsValid) {
        formIsValid = true;
    }

    const formSubmissionHandler = (event) => {
        event.preventDefault();

        if (!enteredEmailIsValid) {
            return;
        }

        // nameInputRef.current.value = ''; => NOT IDEAL, DON'T MANIPULATE THE DOM
        resetEmailInput();
        resetPassInput();
    };

    const emailInputClasses = emailInputHasError
        ? 'form-control invalid'
        : 'form-control';

    const passInputClasses = passInputHasError
        ? 'form-control invalid'
        : 'form-control';


    const loginOrSignup = async (isLogin) => {
        setLoading(true);
        try {
            await Axios.post(
                isLogin ? "auth/login" : "auth/register",
                isLogin
                    ? new URLSearchParams({
                        username: emailRef.current.value,
                        password: passRef.current.value,
                    })
                    : {
                        username: emailRef.current.value,
                        password: passRef.current.value,
                    },
                {
                    headers: {"Content-Type": "application/x-www-form-urlencoded"},
                }
            ).then((res) => {
                if (res.data.status == "fail") {
                    setIsLoggedIn(false);
                    addToast(res.data.errors.msg, {
                        appearance: "error",
                        autoDismiss: true
                    })

                    setLoading(false);
                }

                if (res.data.status == "ok") {
                    setIsLoggedIn(true);
                    setIsVerified(res.data.access_token != null);
                    console.log(res.data);
                    if (res.data.access_token != undefined) {
                        localStorage.setItem(
                            "token",
                            res.data.access_token ?? res.data.tmp_token)
                    }
                    setEmail("");
                    emailRef.current = "";
                    setPass("");
                    passRef.current = "";
                    if (!isLogin && (res.data.data?.msg ?? "").includes("User created")) {
                        setIsEmailVerifingNeeded(true);
                        addToast('verify your account to complete the registration', {
                            appearance: "warning",
                            autoDismiss: true
                        })
                    } else {
                        try {
                            const isAuth =  axios.get("http://sigloyex.com:8080/auth/me" ,{
                                headers:{
                                    'Authorization': "Bearer " + localStorage.getItem("token")
                                }
                            });
                            console.log(isAuth);
                        } catch (error) {

                        }
                        // window.location = "/";
                         history.goBack();
                    }
                }

            });
        } catch (error) {
            console.log(error.toString());
            if (error.toString() == 'Error: Request failed with status code 429') {
                addToast("Too Many Requests", {appearance: "error", autoDismiss: true});
            } else {
                addToast(error.toString(), {appearance: "error", autoDismiss: true});
            }
        }
        setLoading(false);
    };

    const show = () => {
        setShowLogin(!showLogin);
    };

    const [isEmailVerifingNeeded, setIsEmailVerifingNeeded] = useState(false);
    const [showPass, setShowPass] = useState(false);
    const [showPassSignup, setShowPassSignup] = useState(false);

    const context = useContext(Context);


    return (

        <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
            <NavBar/>
            {isLoading && <LoadingDialog/>}
            <div className="container-fluid">
                <div className="col-12  ">
                    <div className="sidenav">
                        <div className="login-main-text" dir={context.locale == "fa" && "rtl"}>
                            <h2 className="Application fw-bold">
                                <FormattedMessage
                                    id="CompanyName"
                                    defaultMessage="Sigloy"
                                />
                                <br/>
                                <FormattedMessage
                                    id="Login.Text.Title"
                                    defaultMessage="Login & Signup"
                                />
                            </h2>
                            <span>
              <FormattedMessage
                  id="Login.Text.SubTitle"
                  defaultMessage="Login From Here"
              />
                </span>
                        </div>
                    </div>
                    <div className="main d-flex justify-content-center">
                        <div className="col-lg-7 col-md-10 col-sm-12">
                            <div className="card p-5 shadow-lg">
                                {showLogin ? (
                                    <h4 className="card-title text-center font-weight-bolder"
                                        dir={context.locale == "fa" && "rtl"}>
                                        <FormattedMessage
                                            id="Login.LoginBtn"
                                            defaultMessage="Login"
                                        />
                                    </h4>
                                ) : (
                                    <h4 className="card-title text-center font-weight-bolder"
                                        dir={context.locale == "fa" && "rtl"}>
                                        <FormattedMessage
                                            id="Login.signinBtn"
                                            defaultMessage="SigninBtn"
                                        />
                                    </h4>
                                )}
                                {isEmailVerifingNeeded && (
                                    <p className="email_verification_needed">
                                        User created, please verify entered email
                                    </p>
                                )}
                                <br/>
                                <div className="text-center small mb-3">
                                    <p className="mb-3 small">
                                        <FormattedMessage
                                            id="Login.Security.context"
                                            defaultMessage="Attention"
                                        />
                                    </p>
                                    <p className="lock pt-1 pb-1 pe-2 ps-2 rounded"
                                       dir={context.locale == "fa" && "rtl"}>
                                        <i className="fas fa-lock me-2"/>
                                        https://www.SigloyEx.com
                                    </p>
                                </div>
                                <br/>

                                {showLogin ? (
                                    <form onSubmit={formSubmissionHandler} className="form-group">
                                        <div
                                            className={`form-floating login-input rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3 ${emailInputClasses}`}
                                            dir={context.locale == "fa" && "rtl"}>
                                            <input
                                                type="email"
                                                className={`form-control`}
                                                id="floatingInput"
                                                placeholder="name@example.com"
                                                value={enteredEmail}
                                                onBlur={emailBlurHandler}
                                                onChange={emailChangeHandler}
                                                ref={emailRef}
                                                required
                                            />
                                            <label htmlFor="floatingInput">
                                                <FormattedMessage
                                                    id="Login.Email"
                                                    defaultMessage="Email"/>
                                            </label>

                                        </div>
                                        {emailInputHasError && (
                                            <p className='error-text'>Please Enter A Valid Email.</p>
                                        )}
                                        <div
                                            className={`form-floating login-input d-flex rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3 ${passInputClasses}`}
                                            dir={context.locale == "fa" && "rtl"}>
                                            <input
                                                type={`${showPass ? "text" :"password"}`}
                                                className="form-control d-flex"
                                                id="floatingPassword"
                                                placeholder="Password"
                                                value={enteredPass}
                                                onChange={passChangedHandler}
                                                onBlur={passBlurHandler}
                                                ref={passRef}
                                                required
                                            />
                                            <span role="button" onClick={()=>setShowPass(!showPass)} className=" showPass ">
                                               {showPass ? <FaEye/> : <FaEyeSlash/>}
                                            </span>
                                            <label htmlFor="floatingPassword">
                                                <FormattedMessage
                                                    id="Login.Password"
                                                    defaultMessage="Password"
                                                />
                                            </label>
                                        </div>
                                        {passInputHasError && (
                                            <p className='error-text'>Please Enter A Valid Password.</p>
                                        )}
                                        <div className="form-group mt-3" dir={context.locale == "fa" && "rtl"}>
                                            <a href="#" onClick={() => history.push("/forgetPassword")} className="text-capitalize small ">
                                                <FormattedMessage
                                                    id="Login.ForgotPassword"
                                                    defaultMessage="ForgotPassword"
                                                />{" "}
                                            </a>
                                        </div>

                                        <button
                                            id="submit"
                                            disabled={!formIsValid}
                                            onClick={(event) => {
                                                loginOrSignup(true);
                                            }}
                                            className="btn rounded-pill text-light mt-3"
                                        >
                                            <FormattedMessage
                                                id="Login.LoginBtn"
                                                defaultMessage="Login"
                                            />
                                        </button>
                                    </form>
                                ) : (
                                    <div className="form-group">
                                        <div
                                            className={`form-floating login-input rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3 ${emailInputClasses}`}>
                                            <input
                                                type="email"
                                                className="form-control "
                                                id="floatingInput"
                                                placeholder="name@example.com"
                                                value={enteredEmail}
                                                onBlur={emailBlurHandler}
                                                onChange={emailChangeHandler}
                                                ref={emailRef}
                                                required
                                            />
                                            <label htmlFor="floatingInput">
                                                <FormattedMessage
                                                    id="Login.Email"
                                                    defaultMessage="Email"
                                                /></label>
                                        </div>
                                        {emailInputHasError && (
                                            <p className='error-text'>Please Enter A Valid Email.</p>)}
                                        <div
                                            className={`form-floating d-flex login-input rounded-pill pt-1 pb-1 pe-5 ps-5 mb-3 ${passInputClasses}`}>
                                            <input
                                                type={`${showPassSignup ? "text" :"password"}`}
                                                className="form-control "
                                                id="floatingPassword"
                                                placeholder="Password"
                                                value={enteredPass}
                                                onBlur={passBlurHandler}
                                                onChange={passChangedHandler}
                                                ref={passRef}
                                                required
                                            />
                                            <span role="button" onClick={()=> setShowPassSignup(!showPassSignup)} className="showPass ">
                                               {showPassSignup ? <FaEye/> : <FaEyeSlash/>}
                                            </span>
                                            <label htmlFor="floatingPassword"> <FormattedMessage
                                                id="Login.Password"
                                                defaultMessage="Password"
                                            /></label>
                                        </div>
                                        {passInputHasError && (
                                            <p className='error-text'>Please Enter A Valid Password.</p>)}
                                        <button
                                            id="submit"
                                            disabled={!formIsValid}
                                            onClick={() => loginOrSignup(false)}
                                            className="btn rounded-pill text-light mt-3"
                                        >
                                            <FormattedMessage
                                                id="Login.signinBtn"
                                                defaultMessage="Signin"
                                            />
                                        </button>
                                    </div>
                                )}

                                {showLogin ? (
                                    <p className={`text-end changer`}>
                                        <span onClick={show}>
                                        <FormattedMessage
                                            id="Login.signinBtn"
                                            defaultMessage="Signin"
                                        />
                                        <MdPlayArrow/>
                                            </span>
                                    </p>
                                ) : (
                                    <p className={`text-end changer`}>
                                        <span onClick={show}>
                                        <FormattedMessage
                                            id="Login.LoginBtn"
                                            defaultMessage="Login"
                                        />
                                        <MdPlayArrow/>
                                            </span>
                                    </p>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    );
};

export default LoginAndSignup;
