import React, {useContext , useEffect} from "react";
import NavBar from "../widgets/NavBar";
import Footer from "../widgets/Footer";
import "../css/bootstrap.css";
import "../css/preferencesSettings.css";
import {makeStyles} from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import {Context} from "../../translation/Wrapper";
import {FormattedMessage} from "react-intl";
import {Theme} from "../ThemeContext";
import {useHistory} from "react-router-dom";
import {LoginContext} from "../../Root";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
import {CgListTree} from "@react-icons/all-files/cg/CgListTree";
import {MdStreetview} from "@react-icons/all-files/md/MdStreetview";
import {HiInboxIn} from "@react-icons/all-files/hi/HiInboxIn";
import {VscReferences} from "@react-icons/all-files/vsc/VscReferences";
import AccountsNavigator from "../widgets/AccountsNavigator";

export default function SettingScreen(props) {
    const context = useContext(Context);
    const theme = useContext(Theme);
    const history = useHistory();
    const {isLoggedIn, setIsLoggedIn} = useContext(LoginContext);
    useEffect(() => {
  if(!isLoggedIn)
      history.push('login');
    }, []);

    const useStyles = makeStyles((theme) => ({
        button: {
            display: 'block',
            marginTop: theme.spacing(2),
        },
        formControl: {
            margin: theme.spacing(1),
            minWidth: 120,
        },
    }));

    const classes = useStyles();


    return (

        <div className={`${context.locale == "fa" && "font-face-farsi"}`}>
            <NavBar/>
            <br/>
            <br/>
            <br/>
            <br/>
            <div className="container-fluid ">
                <div className="row justify-content-center">
                    <div className="col-md-12 col-lg-12 col-xl-10  col-sm-12 col-xs-12">
                        <div className="card shadow preferencesSettings">
                            <div className="card-header" dir={context.locale == "fa" && "rtl"}>

                                <AccountsNavigator />

                                <h2 className="card-title">
                                    <FormattedMessage
                                        id="Settings.Title"
                                        defaultMessage="Preferences Settings"
                                    />
                                </h2>
                            </div>
                            <hr/>
                            <div className="card-body" dir={context.locale == "fa" && "rtl"}>
                                <ul className="list-group list-group-flush options">
                                    <li className="list-group-item order-Confirm-container  justify-content-around">
                                        <span className="order-Confirm">
                                                  <FormattedMessage
                                                      id="Settings.OrderConfirm"
                                                      defaultMessage="OrderConfirm"
                                                  />
                                        </span>
                                        <span className="explanation">
                By activating Order Reminder, a pop-up tip will remind you to
                double check when placing an order.
              </span>
                                        <span className="setting-center">
                <input type="checkbox" name="A" className="checkbox"/>
              </span>
                                    </li>
                                    <li className="list-group-item order-Confirm-container  justify-content-around">
                                        <span className="order-Confirm">
                                            <FormattedMessage
                                                id="Settings.UseCETasFees"
                                                defaultMessage="UseCETasFees"
                                            />
                                        </span>
                                        <span className="explanation">

              </span>
                                        <span className="setting-center">
                <input type="checkbox" name="A" className="checkbox"/>
              </span>
                                    </li>
                                    <li className="list-group-item order-Confirm-container  justify-content-around">
                                        <div className={`order-Confirm  ${context.locale != "fa" && "me-lg-5"} `}>
                                            <FormattedMessage
                                                id="Settings.LanguageSettings"
                                                defaultMessage="LanguageSettings"
                                            />
                                        </div>
                                        <span className="explanation"/>
                                        <FormControl  className={classes.formControl}>
                                            {/*<InputLabel style={{color:'#8c8c8c'}} id="demo-controlled-open-select-label">*/}
                                            {/*    <FormattedMessage*/}
                                            {/*        id="Settings.Language"*/}
                                            {/*        defaultMessage="Language"*/}
                                            {/*    />*/}
                                            {/*</InputLabel>*/}
                                            <Select
                                                style={{color:'#8c8c8c' , textAlign:"center"}}
                                                IconComponent={'ArrowDropDownIcon'}
                                                labelId="demo-controlled-open-select-label"
                                                id="demo-controlled-open-select"
                                                defaultValue={'en'}
                                                value={context.locale} onChange={context.selectLanguage}
                                            >
                                                <MenuItem value='en'>English</MenuItem>
                                                <MenuItem value='fa'></MenuItem>
                                            </Select>
                                        </FormControl>

                                    </li>
                                    {/*<li className="list-group-item order-Confirm-container  justify-content-around">*/}
                                    {/*    <div className={`order-Confirm  ${context.locale != "fa" && "me-lg-5"} `}>*/}
                                    {/*        <FormattedMessage*/}
                                    {/*            id="Settings.ExchangeRate"*/}
                                    {/*            defaultMessage="ExchangeRate"*/}
                                    {/*        />*/}
                                    {/*    </div>*/}
                                    {/*    <span className="explanation"/>*/}

                                    {/*    <FormControl className={classes.formControl}>*/}
                                    {/*        <InputLabel id="demo-controlled-open-select-label">*/}
                                    {/*            <FormattedMessage*/}
                                    {/*                id="Settings.Age"*/}
                                    {/*                defaultMessage="Age"*/}
                                    {/*            />*/}
                                    {/*        </InputLabel>*/}
                                    {/*        <Select*/}
                                    {/*            labelId="demo-controlled-open-select-label"*/}
                                    {/*            id="demo-controlled-open-select"*/}
                                    {/*        >*/}
                                    {/*            <MenuItem value="">*/}
                                    {/*                <em>None</em>*/}
                                    {/*            </MenuItem>*/}
                                    {/*            <MenuItem value={10}>Ten</MenuItem>*/}
                                    {/*            <MenuItem value={20}>Twenty</MenuItem>*/}
                                    {/*            <MenuItem value={30}>Thirty</MenuItem>*/}
                                    {/*        </Select>*/}
                                    {/*    </FormControl>*/}

                                    {/*</li>*/}
                                    <li className="list-group-item order-Confirm-container  justify-content-around">
                                        <span className="order-Confirm">
                                            <FormattedMessage
                                                id="Settings.DarkMode"
                                                defaultMessage="DarkMode"
                                            />
                                        </span>
                                        <span className="explanation">
                By activating Order Reminder, a pop-up tip will remind you to
                double check when placing an order.
              </span>
                                        <span className="setting-center">
                <input type="checkbox" name="A" className="checkbox"  onChange={theme.themeChanger} checked={theme.theme === 'dark'}/>
              </span>
                                    </li>
                                    <li className="list-group-item order-Confirm-container  justify-content-around">
                                        <span className="order-Confirm">
                                                 <FormattedMessage
                                                     id="Settings.E-mailSubscriptions"
                                                     defaultMessage="E-mailSubscriptions"
                                                 />
                                        </span>
                                        <span className="explanation">
                By activating Notifications, you will receive related Email
                notifications from Sigloy.
              </span>
                                        <span className="setting-center">
                <input type="checkbox" name="A" className="checkbox"/>
              </span>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <br/>
            <br/>
            <Footer/>

        </div>
    );
}
